import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { ICart } from '@schemas/modules/cart/cart';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { CombinationId } from '@schemas/modules/core/base-types';
import { PaymentCaptureMode } from '@core/constants/cart-status';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-selected-car-rentals',
    templateUrl: './selected-car-rentals.component.html',
    styleUrls: ['./selected-car-rentals.component.scss'],
})
export class SelectedCarRentalsComponent implements OnInit {
    @Input() serviceList = false;
    @Input() combId!: CombinationId;
    @Input() carRentalCartData!: ICart;
    @Input() carRentalPackages: CarRentalPackage[] = [];
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Input() detailPage = false;
    @Input() selectedChangeOptions: EventEmitter<void> = new EventEmitter<void>();
    @Output() loadCarRentalPackages: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeCarRentals: EventEmitter<void> = new EventEmitter<void>();
    @Output() modifyCarRentalsOptions: EventEmitter<void> = new EventEmitter<void>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() newCarRentalPackageSelected: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
    @Output() showCarRentalDetails: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() showPackagesFlag: EventEmitter<boolean> = new EventEmitter<boolean>();
    public selectedTransfer!: Transfer;

    constructor(public platformService: PlatformService) {}

    ngOnInit() {}

    public onRemove(): void {
        this.removedItem.emit();
    }

    public onLoadCarRentalPackages() {
        this.loadCarRentalPackages.emit(this.carRentalCartData.items[0].selectedItem.data);
    }

    public onChangeCarRentals(): void {
        this.changeCarRentals.emit();
    }

    public onModify(): void {
        this.modifyCarRentalsOptions.emit();
    }

    public onDebugDialogOpened(): void {}

    public onChangeCarRentalOptions(carRental: CarRental): void {
        if (this.serviceList) {
            this.newCarRentalPackageSelected.emit(carRental);
        } else {
            this.modifyCarRentalsOptions.emit();
        }
    }

    public onCancel(): void {
        this.cancel.emit();
    }
}
