import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { ServiceId } from '@core/constants/service-id.const';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';

@Component({
    selector: 'tz-mob-accommodation-guest-info',
    templateUrl: './mob-accommodation-guest-info.component.html',
    styleUrls: ['./mob-accommodation-guest-info.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccommodationGuestInfoComponent implements OnInit {
    @Input() accommodation!: Accommodation;

    public bookingOptions!: ICartSelectedItemBookingOption[];
    public rooms!: number;
    public childCount = 0;
    public adultsCount = 0;

    constructor(private cartService: CartService, private searchDataService: SearchDataService) {}

    public ngOnInit(): void {
        this.bookingOptions = this.cartService.getCartData(ServiceId.ACC)?.items[0]?.selectedItem.bookingOptions;
        this.rooms = this.bookingOptions?.length || this.accommodation.numberOfRooms;

        this.getPeopleCount();
    }

    private getPeopleCount(): void {
        const { passengers } = this.searchDataService.getSearch(ServiceId.ACC);

        this.childCount = (passengers.children || 0) + (passengers.infants || 0);
        this.adultsCount = passengers.adults || 0;
    }
}
