import { Component, Input, OnInit } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { RentalTerm } from '@schemas/modules/car-rentals/car-rental';

@Component({
    selector: 'tz-mob-car-terms',
    templateUrl: './mob-car-terms.component.html',
    styleUrls: ['./mob-car-terms.component.scss'],
})
export class MobCarTermsComponent implements OnInit {
    @Input() carRentalTerms: RentalTerm[] = [];
    private data!: {
        carRentalTerms: any;
    };

    constructor(private toolService: ToolsService) {}

    ngOnInit() {
        this.carRentalTerms = ToolsService.deepCopy(this.data.carRentalTerms);
    }
}
