import { Component, Input, OnInit } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { ICartItem } from '@schemas/modules/cart/cart-item';

@Component({
    selector: 'tz-mob-selected-train-details-item',
    templateUrl: './mob-selected-train-details-item.component.html',
    styleUrls: ['./mob-selected-train-details-item.component.scss'],
})
export class MobSelectedTrainDetailsItemComponent implements OnInit {
    @Input() public train!: ICartItem;
    @Input() public currentTripIndex = 0;
    public optionTrain!: TRAIN;
    public totalPassengers = 0;
    public arrivalCityName = '';
    public departureCityName = '';

    public ngOnInit(): void {
        this.prepareTrainData();
    }

    private prepareTrainData(): void {
        this.optionTrain = this.train.selectedItem?.data?.onward;

        const { adults, children } = this.train.searchReq.passengers;
        const { from, to } = this.train.searchReq.searchData;

        this.totalPassengers = adults + children;
        this.departureCityName = from.cityName;
        this.arrivalCityName = to.cityName;

        if (this.currentTripIndex) {
            this.departureCityName = to.cityName;
            this.arrivalCityName = from.cityName;
        }
    }
}
