import { AccommodationsResponseSection } from '@schemas/modules/accommodations/accommodations-response';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { Room } from '@schemas/modules/accommodations/room';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { ICartSelectedItem } from '@schemas/modules/cart/cart-selected-item';
import { ToolsService } from '@core/utils/tools-service';

export class AccommodationsUtilsService {
    public static getCount(inCity: boolean, acc: AccommodationsResponseSection[]): number {
        for (const accData of acc) {
            if (accData.section === 1) {
                if (inCity) {
                    return accData.count;
                }
            } else if (!inCity) {
                return accData.count;
            }
        }
        return 0;
    }

    public static getMaxRoomSelectionNumber(
        roomOption: { personCount: number; roomsLeft: number },
        maxRoomSelectionNumber: number
    ): number {
        const value = roomOption.roomsLeft - roomOption.personCount;
        return Math.min(value, maxRoomSelectionNumber);
    }

    public static getSelectedRoomQuantity(
        rooms: Room[],
        bookingOptions: ICartSelectedItemBookingOption[] = []
    ): number {
        let count = 0;
        for (const room of rooms) {
            for (const roomOption of room.roomOffers) {
                count += roomOption.personCount;
            }
        }

        for (const option of bookingOptions) {
            if (CartUtils.isBooked(option.status)) {
                count++;
            }
        }

        return count;
    }

    public static checkShowTerms(roomOption: RoomOption, isUserLogged: boolean): boolean {
        return (
            roomOption.roomCancellation.otherPolicies.length > 0 ||
            roomOption.roomCancellation.cancellationPolicy === 'Non-refundable' ||
            (roomOption.roomCancellation.isFree && roomOption.roomCancellation.endDateTime !== '') ||
            roomOption.payAt === 'Pay on arrival' ||
            (roomOption.discounts.length > 0 && (roomOption.packaging ? isUserLogged : !roomOption.packaging))
        );
    }

    public static getCartRoomSelections(cartData: IAccommodationsCartData): {
        bookedOptions: { [key: string]: number };
        validatedOption: { [key: string]: number };
    } {
        const bookedOptions: { [key: string]: number } = {};
        const validatedOption: { [key: string]: number } = {};

        const validCart = cartData?.items?.length;
        if (!validCart) {
            return { bookedOptions, validatedOption };
        }

        const bookingOptions = cartData.items[0].selectedItem.bookingOptions;

        for (const option of bookingOptions) {
            const roomOfferId = option.selectedRoom?.roomOfferId;
            if (!roomOfferId) {
                continue;
            }

            const isBookedOffer = CartUtils.isBooked(option.status);
            if (isBookedOffer) {
                const count = bookedOptions[roomOfferId];
                bookedOptions[roomOfferId] = count ? count + 1 : 1;
            }

            if (!isBookedOffer && !CartUtils.isFailed(option.status)) {
                const count = validatedOption[roomOfferId];
                validatedOption[roomOfferId] = count ? count + 1 : 1;
            }
        }

        return { bookedOptions, validatedOption };
    }

    public static getCartRooms(selectedItem: ICartSelectedItem): {
        rooms: Room[];
        options: { [key: number]: ICartSelectedItemBookingOption[] };
    } {
        const selectedRooms = {
            rooms: [] as Room[],
            options: [] as { [key: number]: ICartSelectedItemBookingOption[] },
        };
        const rooms = selectedItem.data.rooms as Room[];
        const foundRooms: { [key: string]: number } = {};

        for (const option of selectedItem.bookingOptions) {
            let optionFound = false;

            for (const room of rooms) {
                let found = false;
                const offers: RoomOption[] = [];

                for (const offer of room.roomOffers) {
                    if (option.selectedRoom?.roomOfferId === offer.id) {
                        option.selectedRoom.selectedOffer = offer;
                        option.selectedRoom.roomType = room.displayName;
                        found = true;
                        offers.push(offer);
                    }
                }
                if (found) {
                    let roomIdx = foundRooms[room.id];
                    if (roomIdx === undefined) {
                        const copyRoom = ToolsService.deepCopy(room);
                        copyRoom.roomOffers = [];
                        selectedRooms.rooms.push(copyRoom);
                        roomIdx = selectedRooms.rooms.length - 1;
                        selectedRooms.options[roomIdx] = [];
                        foundRooms[room.id] = roomIdx;
                    }
                    selectedRooms.rooms[roomIdx].roomOffers = selectedRooms.rooms[roomIdx].roomOffers.concat(offers);
                    selectedRooms.options[roomIdx].push(option);
                    optionFound = true;
                }
            }
            if (!optionFound && option.selectedRoom) {
                // room was removed from list because not available anymore
                // we create a fake room structure
                let roomIdx = foundRooms[option.selectedRoom.roomId];
                if (roomIdx === undefined) {
                    selectedRooms.rooms.push({
                        amenities: option.selectedRoom.amenities,
                        facility1: option.selectedRoom.facility1,
                        facility2: option.selectedRoom.facility2,
                        taxInfo: option.selectedRoom.taxInfo,
                        images: option.selectedRoom.images,
                        broker: option.selectedRoom.selectedOffer.broker,
                        displayName: option.selectedRoom.roomType,
                        roomOffers: [] as RoomOption[],
                    } as Room);
                    roomIdx = selectedRooms.rooms.length - 1;
                    selectedRooms.options[roomIdx] = [];
                    foundRooms[option.selectedRoom.roomId] = roomIdx;
                }
                selectedRooms.rooms[roomIdx].roomOffers = selectedRooms.rooms[roomIdx].roomOffers.concat(
                    option.selectedRoom.selectedOffer
                );
                selectedRooms.options[roomIdx].push(option);
            }
        }

        return selectedRooms;
    }
}
