import { Component, Input, OnInit, Inject } from '@angular/core';

import { TermsAndConditions } from '@schemas/modules/trains/train-terms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-train-terms',
    templateUrl: './terms.component.html',
    styleUrls: ['./terms.component.scss'],
})
export class TrainTermsComponent implements OnInit {
    public terms: TermsAndConditions[] = [];
    public operator!: string;
    public showViaRailTerms = false;

    constructor(
        @Inject(MAT_DIALOG_DATA) public dialogData: any,
        private mdDialogRef: MatDialogRef<TrainTermsComponent>,
        private dialogService: DialogService
    ) {
        const termsData = this.dialogData;
        this.operator = ToolsService.deepCopy(termsData.operator);
        this.terms = ToolsService.deepCopy(termsData.terms);
    }

    ngOnInit(): void {}

    public closeDialog(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }

    public trackByFn(index: number) {
        return index;
    }
}
