import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';

import { FlightSummary, FlightSummaryType } from '@schemas/modules/flights/flight-summary.model';
import { FLIGHT_OPTIONS_STATIC } from '@core/constants/flight-options-static';

@Component({
    selector: 'tz-flight-list-item-desc-block',
    templateUrl: './flight-list-item-desc-block.component.html',
    styleUrls: ['./flight-list-item-desc-block.component.scss'],
})
export class FlightListItemDescBlockComponent implements OnChanges, OnInit {
    @Input() returnSummary!: FlightSummary;
    @Input() departureSummary!: FlightSummary;
    @Input() isMyTipTicket = false;
    @Input() itemType: FlightSummaryType = 'depart';

    public flightSummary!: FlightSummary;
    public optionsStatic = FLIGHT_OPTIONS_STATIC;

    constructor() {}

    get isDepartItem(): boolean {
        return this.itemType === 'depart' ? true : false;
    }

    ngOnChanges(changes: SimpleChanges) {
        if (
            changes['departureSummary'] &&
            (changes['departureSummary'].previousValue?.id !== changes['departureSummary'].currentValue?.id ||
                changes['departureSummary'].previousValue.classes[0] !==
                    changes['departureSummary'].currentValue.classes[0])
        ) {
            this.flightSummary = this.isDepartItem ? this.departureSummary : this.returnSummary;
        }
    }

    ngOnInit() {
        this.setFlightSummeryData();
    }

    setFlightSummeryData(): void {
        this.flightSummary = this.isDepartItem ? this.departureSummary : this.returnSummary;
    }
}
