import { Pipe, PipeTransform } from '@angular/core';
import { FlightSummary } from '@schemas/modules/flights/flight-summary.model';

@Pipe({
    name: 'flightAirlineName',
})
export class FlightAirlineNamePipe implements PipeTransform {
    transform(flightSummary: FlightSummary): string {
        if (flightSummary.airlineName) {
            return flightSummary.airlineName;
        }

        if (flightSummary.operatedByAirlines[0]) {
            return flightSummary.operatedByAirlines[0];
        }

        return flightSummary.airline;
    }
}
