import { Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { GeneralSettings } from '@core/constants/general-settings';
import { FitBoundsOptions, LngLat, LngLatBounds, LngLatBoundsLike, Map, ScaleControl } from 'mapbox-gl';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { MapMarkers } from '@core/constants/mapbox-gl-const';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-mob-accommodation-map',
    templateUrl: './mob-accommodation-map.component.html',
    styleUrls: ['./mob-accommodation-map.component.scss'],
})
export class MobAccommodationMapComponent implements OnInit {
    @Input() accommodation!: Accommodation;
    @ViewChild('mapBoxContainer') mapBoxContainer?: ElementRef;

    public mapStyle = GeneralSettings.mapbox.mapStyle;
    public mapboxRef!: Map;
    public mapExpanded = false;
    public locationInfo = {
        hotelName: '',
        searchType: '',
        distance: '',
    };
    public markers: {
        lngLat: LngLat;
        marker: string;
    }[] = [];
    public fitBounds!: LngLatBoundsLike;
    public fitBoundsOptions: FitBoundsOptions = {};

    constructor(private searchDataService: SearchDataService) {}

    @HostListener('document:click', ['$event'])
    onClick(event: MouseEvent) {
        let targetElement = event.target as HTMLElement;
        let outsideClick = true;

        do {
            if (targetElement.tagName === 'TZ-ACCOMMODATION-MAP') {
                outsideClick = false;
                break;
            }

            targetElement = targetElement.parentNode as HTMLElement;
        } while (targetElement);

        if (outsideClick) {
            this.disableMapInteractions();
        } else {
            this.enableMapInteractions();
        }
    }

    ngOnInit() {
        this.setHotelMarkers();
    }

    public onMapLoad(map: Map): void {
        this.mapboxRef = map;

        const scale = new ScaleControl({
            maxWidth: 80,
            unit: 'metric',
        });
        this.mapboxRef.addControl(scale);
        this.disableMapInteractions();
    }

    public toggleMapSize(): void {
        if (this.mapBoxContainer) {
            this.mapExpanded = !this.mapExpanded;
            this.mapBoxContainer.nativeElement.style.height = (this.mapExpanded ? 370 : 210) + 'px'; // set height
            this.mapboxRef.resize();
        }
    }

    public zoomMap(zoomin = false): void {
        this.mapboxRef.setZoom(this.mapboxRef.getZoom() + (zoomin ? 0.5 : -0.5));
    }

    private setHotelMarkers(): void {
        this.markers.push({
            lngLat: new LngLat(
                Number(this.accommodation.location.longitude),
                Number(this.accommodation.location.latitude)
            ),
            marker: MapMarkers.PURPLE,
        });

        if (this.accommodation.aerialDistance && this.accommodation.aerialDistance > 0) {
            const searchInfos = this.searchDataService.getSearchData(ServiceId.ACC);

            if (searchInfos && searchInfos.address) {
                this.markers.push({
                    lngLat: new LngLat(Number(searchInfos.address.longitude), Number(searchInfos.address.latitude)),
                    marker: MapMarkers.ORANGE,
                });

                this.locationInfo = {
                    hotelName: this.accommodation.name,
                    searchType: searchInfos.address.cityName === '' ? 'SEARCH_POINT' : 'CITY_CENTER',
                    distance: Math.round(this.accommodation.aerialDistance) + ' km',
                };
            }
        }

        if (this.markers.length === 1) {
            this.fitBoundsOptions.maxZoom = 15;
        } else {
            const latLngBounds = new LngLatBounds();
            for (const marker of this.markers) {
                latLngBounds.extend([marker.lngLat.lng, marker.lngLat.lat]);
            }
            this.fitBounds = latLngBounds;
        }
    }

    private disableMapInteractions(): void {
        if (this.mapboxRef) {
            this.mapboxRef.scrollZoom.disable();
            this.mapboxRef.boxZoom.disable();
            this.mapboxRef.dragRotate.disable();
            this.mapboxRef.dragPan.disable();
            this.mapboxRef.keyboard.disable();
            this.mapboxRef.doubleClickZoom.disable();
            this.mapboxRef.touchZoomRotate.disable();
        }
    }

    private enableMapInteractions(): void {
        if (this.mapboxRef) {
            this.mapboxRef.scrollZoom.enable();
            this.mapboxRef.boxZoom.enable();
            this.mapboxRef.dragRotate.enable();
            this.mapboxRef.dragPan.enable();
            this.mapboxRef.keyboard.enable();
            this.mapboxRef.doubleClickZoom.enable();
            this.mapboxRef.touchZoomRotate.enable();
        }
    }
}
