import { Component, Input, OnInit } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { Facility } from '@schemas/modules/trains/train-class';
import { TrainTermsComponent } from '../terms/terms.component';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-facilities-list',
    templateUrl: './facilities-list.component.html',
    styleUrls: ['./facilities-list.component.scss'],
})
export class FacilitiesListComponent implements OnInit {
    @Input() public train: TRAIN | undefined;
    public facilities!: Facility[] | undefined;
    public fareCondition: any;

    constructor(private dialogService: DialogService) {}

    public ngOnInit(): void {
        this.formatData();
    }

    public openTrainTerms(event: Event): void {
        event.stopPropagation();

        this.dialogService.openServiceDialog(TrainTermsComponent, {
            width: undefined,
            maxHeight: '90vh',
            panelClass: 'tz-terms-dialog-large',
            maxWidth: '940px',
            data: {
                operator: this.train?.operator,
                terms: this.train?.trainClass.ClassDescription.termsandConditionsList,
            },
        });
    }

    private formatData(): void {
        this.facilities = this.train?.trainClass?.ClassDescription?.facilities;
        this.fareCondition = this.train?.trainClass?.ClassDescription.fareCondition;
    }
}
