import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { BestValType } from '@schemas/modules/search/search';
import { ResultsTools } from '@services/modules/results/services/tools.shared.services';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-car-rental-list-item',
    templateUrl: './car-rental-list-item.component.html',
    styleUrls: ['./car-rental-list-item.component.scss'],
})
export class CarRentalListItemComponent implements OnInit {
    @Input() carRental: CarRental | null | undefined;
    @Input() selectable = true;
    @Input() selectedRental = false;
    @Input() oldPrice = 0;
    @Output() selectedCarRental: EventEmitter<void> = new EventEmitter<void>();
    @Output() openPopupModel = new EventEmitter<{ carRental: CarRental; modelType: string }>();

    public opened = false;
    public bestValType: BestValType | undefined;

    constructor() {}

    ngOnInit(): void {
        if (this.carRental && this.carRental.bestValType) {
            this.bestValType = ResultsTools.getBestValType(this.carRental.bestValType, ServiceId.CAR_RENTAL);
        }
    }

    public selectCarRental() {
        this.selectedCarRental.emit();
    }

    public openPopup(modelType: string): void {
        if (this.carRental) {
            this.openPopupModel.emit({
                carRental: this.carRental,
                modelType,
            });
        }
    }
}
