import { Injectable } from '@angular/core';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { ServiceId } from '@core/constants/service-id.const';
import { Observable, pipe } from 'rxjs';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { HttpRequestType } from '@core/constants/http-request-type';
import { ICart } from '@schemas/modules/cart/cart';
import { Room } from '@schemas/modules/accommodations/room';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { AccGalleryRes } from '@schemas/modules/accommodations/accommodation-gallery.model';
import { HttpContext } from '@angular/common/http';
import { SEARCH_RESULT_PROCESSING } from '@services/modules/core/loader.service';
import { AccommodationsUtilsService } from '../utils/accommodations.utils';

@Injectable({
    providedIn: 'root',
})
export class AccommodationsDataService extends ResultService {
    public override readonly serviceId = ServiceId.ACC;

    public getAccommodation(id: string): Observable<Accommodation> {
        const params = {
            entity: 'rateCheck',
            id: this.searchDataService.getCombId(),
            dataId: this.searchDataService.getDataId(this.serviceId),
            serviceId: this.serviceId,
            selected_id: id,
            currency: this.settingsService.getCurrency(),
            showLoader: true,
            lang: this.settingsService.langCode,
            sort_by: 'bestValue',
        };
        const httpOptions = {
            context: new HttpContext().set(SEARCH_RESULT_PROCESSING, true),
        };
        return this.apiCallOptions(HttpRequestType.GET, params, httpOptions);
    }

    public getAccommodationReviews(accommodation: Accommodation) {
        const params = {
            entity: 'additionalDetails',
            id: accommodation.oleryId,
            lang: this.settingsService.langCode,
            latitude: accommodation.location?.latitude,
            longitude: accommodation.location?.longitude,
            country: accommodation.country,
        };

        return this.apiCallObs(HttpRequestType.GET, params);
    }

    public addAccommodationToCart(accommodation: Accommodation): Observable<ICart> {
        const cartData = this.getSelectedAccommodationsData();
        const { bookedOptions } = AccommodationsUtilsService.getCartRoomSelections(cartData);

        // form the selected room offers
        const bookingOptions: any = [];
        accommodation.rooms.forEach((room: Room) => {
            room.roomOffers.forEach((offer: RoomOption) => {
                offer.personCount -= bookedOptions[offer.id] ?? 0;
                for (let i = 0; i < offer.personCount; i++) {
                    bookingOptions.push({
                        selectedRoom: {
                            hotelId: accommodation.id,
                            roomId: room.id,
                            roomType: room.displayName,
                            selectedOffer: {
                                id: offer.id,
                            },
                            roomOfferId: offer.id,
                        },
                    });
                }
            });
        });
        const options = [
            {
                id: accommodation.id,
                hotelCode: accommodation.giataID,
                bookingOptions,
            },
        ];
        return this.addToCart(options);
    }

    public getSelectedAccommodationsData(): IAccommodationsCartData {
        return this.getServiceCartItems();
    }

    public getHotelImages(id: string, actionId: string, imageProvider: string): Observable<AccGalleryRes> {
        const params = {
            entity: 'accommodations',
            action: 'searchhotel',
            actionEntity: 'gallery',
            id,
            actionId,
            imageProvider,
        };
        return this.apiCall(HttpRequestType.GET, params);
    }
}
