import { ChangeDetectionStrategy, Component, Inject, Input, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalLocation } from '@schemas/modules/car-rentals/car-rental-location';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-car-rental-location-dialog',
    templateUrl: './car-rental-location-dialog.component.html',
    styleUrls: ['./car-rental-location-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CarRentalLocationDialogComponent implements OnInit {
    @Input() carRental!: CarRental;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<CarRentalLocationDialogComponent>,
        private dialogService: DialogService
    ) {
        this.carRental = ToolsService.deepCopy(data.carRental);
    }

    get getCarRentalLocations(): CarRentalLocation[] {
        if (this.carRental.pickupDropOffDiff) {
            return [this.carRental.pickupLocation, this.carRental.dropOffLocation];
        } else {
            return [this.carRental.pickupLocation];
        }
    }

    ngOnInit() {}

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }
}
