import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { TrainClass } from '@schemas/modules/trains/train-class';
import {
    SelectedOption,
    SupplierClassMinPrice,
    TrainSelection,
} from '@schemas/modules/trains/train-search-query-params';
import { TrainService } from '@services/modules/transport/trains/trains.service';
import { TzServiceTabs } from '../../../../tabsUtils/tz-tabs-service';

@Component({
    selector: 'tz-mob-trains-list-item',
    templateUrl: './mob-trains-list-item.component.html',
    styleUrls: ['./mob-trains-list-item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobTrainsListItemComponent implements OnInit {
    @Input() train: TRAIN | undefined;
    @Input() trainClass: TrainClass | undefined;
    @Input() selectedOption!: SelectedOption;
    @Input() isRoundTrip!: boolean;
    @Input() isSelected = false;
    @Input() cartItem: any;
    @Input() totalPassengers = 0;
    @Input() trainType: 'depart' | 'return' = 'depart';
    @Input() oldPrice = 0;
    public supplierClassMinPrice: SupplierClassMinPrice = {};
    public changedPriceDiff = '';

    constructor(private trainService: TrainService, private serviceTabs: TzServiceTabs) {}

    get price(): number {
        let listPrice: number;
        if (this.selectedOption?.onward && this.selectedOption.onward?.id === this.train?.id) {
            listPrice = this.supplierClassMinPrice[this.selectedOption.onward.tzClass]?.listPrice;
        } else if (this.selectedOption?.return && this.selectedOption.return?.id === this.train?.id) {
            listPrice = this.supplierClassMinPrice[this.selectedOption.return.tzClass]?.listPrice;
        } else {
            listPrice = this.train?.prices?.listPrice ? this.train?.prices?.listPrice : 0;
        }

        return listPrice;
    }

    get selectedTrain(): TrainSelection | undefined {
        if (this.selectedOption?.onward && this.selectedOption.onward?.id === this.train?.id) {
            this.trainType = 'depart';
            return this.selectedOption.onward;
        }

        if (this.selectedOption?.return && this.selectedOption.return?.id === this.train?.id) {
            return this.selectedOption.return;
        }

        return undefined;
    }

    public ngOnInit(): void {
        this.fetchServicePrices();
        this.initData();
    }

    private fetchServicePrices(): void {
        if (this.train && !!this.selectedTrain) {
            this.supplierClassMinPrice = this.trainService.getServicePrices(this.train, this.selectedTrain);
        }
    }

    private initData(): void {
        const currentCartPrice = this.cartItem?.itemPrice?.listPrice;

        if (this.oldPrice > 0 && currentCartPrice) {
            this.changedPriceDiff = this.serviceTabs.updatePriceChanging(this.oldPrice, currentCartPrice);
        }
    }
}
