import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';

@Component({
    selector: 'tz-flight-provider-image',
    templateUrl: './flight-provider-image.component.html',
    styleUrls: ['./flight-provider-image.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FlightProviderImageComponent implements OnInit {
    @Input() className = '';
    @Input() provider!: string;

    public imageClassName = '';
    public providerImage = '';

    constructor() {}

    ngOnInit(): void {
        this.imageClassName = 'flight-provider-image ' + this.className;
        this.providerImage = '/assets/icons/web/img/airHex/' + this.provider + '.png';
    }
}
