import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { SortByValueOption } from '@schemas/modules/core/sort-by-value.option';

@Component({
    selector: 'tz-mob-filter-navbar',
    templateUrl: './mob-filter-navbar.component.html',
    styleUrls: ['./mob-filter-navbar.component.scss'],
})
export class MobFilterNavbarComponent implements OnInit, OnChanges {
    @Input() sortKey: string | undefined = '';
    @Input() sortByOptions: SortByValueOption[] = [];
    @Input() isEnableClearFilter = false;
    @Input() filtersCount: number | undefined;
    @Input() displayMap = false;
    @Input() skeletonLoading = true;
    @Output() openFilter: EventEmitter<void> = new EventEmitter<void>();
    @Output() resetFilter: EventEmitter<void> = new EventEmitter<void>();
    @Output() openSort: EventEmitter<void> = new EventEmitter<void>();
    @Output() showMap: EventEmitter<void> = new EventEmitter<void>();

    public sortLabel = '';

    constructor() {}

    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges) {
        this.setSortText();
    }

    public onOpenFilter(): void {
        this.openFilter.emit();
    }

    public onResetFilter(): void {
        this.resetFilter.emit();
    }

    public onShowMap(): void {
        this.showMap.emit();
    }

    public onOpenSort(): void {
        this.openSort.emit();
    }

    private setSortText(): void {
        this.sortByOptions.map((sortByOption) => {
            if (this.sortKey && sortByOption.sort === this.sortKey) {
                this.sortLabel = sortByOption.label;
            }
        });
    }
}
