import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { FlightSummary, FlightSummaryType } from '@schemas/modules/flights/flight-summary.model';

@Component({
    selector: 'tz-mob-flight-list-item-info-block',
    templateUrl: './mob-flight-list-item-info-block.component.html',
    styleUrls: ['./mob-flight-list-item-info-block.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobFlightListItemInfoBlockComponent implements OnInit {
    @Input() flight!: Flight;
    @Input() flightSummary!: FlightSummary;
    @Input() itemType: FlightSummaryType = 'depart';
    @Input() packages: Flight[] | undefined = [];

    public stops: number[] = [];

    constructor() {}

    get isDepartItem(): boolean {
        return this.itemType === 'depart';
    }

    ngOnInit() {
        this.setFlightSummeryData();
    }

    setFlightSummeryData(): void {
        this.flightSummary = this.isDepartItem ? this.flight.departureSummary : this.flight.returnSummary;
        this.stops = Array(this.setAirportStopRoutes().length);
    }

    private setAirportStopRoutes(): string[] {
        let airportStopRoutesArr: string[] = [];
        if (!this.flightSummary.airportStopRoutes || !this.flightSummary.airportStopRoutes.length) {
            return airportStopRoutesArr;
        }

        if (this.flightSummary.airportStopRoutes?.length === 1) {
            airportStopRoutesArr.push(this.flightSummary.airportStopRoutes[0].code);

            if (this.flightSummary.changeInAirport) {
                airportStopRoutesArr.push(this.flightSummary.airportStopRoutes[1].code);
            }
        }

        if (this.flightSummary.airportStopRoutes?.length === 2) {
            airportStopRoutesArr = this.flightSummary.airportStopRoutes.map((routes) => routes.code);
        }
        return airportStopRoutesArr;
    }
}
