import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { Route } from '@schemas/modules/trains/train-route';
import { Location, TravelingLocationInfo } from '@schemas/modules/trains/train-location';

@Component({
    selector: 'tz-trains-list-item-info',
    templateUrl: './trains-list-item-info.component.html',
    styleUrls: ['./trains-list-item-info.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TrainsListItemInfoComponent implements OnInit {
    @Input() departureDate!: string;
    @Input() arrivalDate!: string;
    @Input() duration!: string;
    @Input() departureLocation!: TravelingLocationInfo;
    @Input() arrivalLocation!: TravelingLocationInfo;
    @Input() stops: Route[] = [];
    @Input() layoverName!: string | undefined;

    public origin = '';
    public destination = '';

    constructor() {}

    get isSameStation() {
        if (!this.stops.length) {
            return true;
        }

        return this.stops[0].arrival.location.code === this.stops[1].departure.location.code;
    }

    ngOnInit() {
        this.setOriginAndDestinationValues();
    }

    private setOriginAndDestinationValues(): void {
        const departureLocation = this.departureLocation.location;
        const arrivalLocation = this.arrivalLocation.location;
        this.origin = departureLocation.name;
        this.destination = arrivalLocation.name;
    }
}
