import { Component, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { PickerController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { ServicePrices } from '@schemas/modules/core/service-prices';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';
import { ModalService } from '@services/modal.service';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-mob-accommodation-room-offer',
    templateUrl: './mob-accommodation-room-offer.component.html',
    styleUrls: ['./mob-accommodation-room-offer.component.scss'],
})
export class MobAccommodationRoomOfferComponent implements OnInit, OnChanges {
    @Input() roomOption!: RoomOption;
    @Input() roomPrice!: ServicePrices;
    @Input() stayNumber!: number;
    @Input() isUserLogged!: boolean;
    @Input() roomName?: string = '';
    @Input() recommended?: boolean = false;
    @Input() lastItem?: boolean = false;
    @Input() maxRoomSelectionNumber = 0;
    @Output() roomCountChanged: EventEmitter<void> = new EventEmitter<void>();
    @Output() getRoomPrice: EventEmitter<{ price: number; id: string }> = new EventEmitter<{
        price: number;
        id: string;
    }>();
    @Output() openTermsModal: EventEmitter<void> = new EventEmitter<void>();

    public maxValue = 0;

    constructor(
        private pickerCtrl: PickerController,
        private translateService: TranslateService,
        private modalService: ModalService,
        private scrollService: ScrollService
    ) {}

    get checkShowTerms(): boolean {
        return AccommodationsUtilsService.checkShowTerms(this.roomOption, this.isUserLogged);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (
            changes['maxRoomSelectionNumber'] &&
            (changes['maxRoomSelectionNumber'] || changes['maxRoomSelectionNumber'] === 0)
        ) {
            const personCount = this.roomOption.personCount;
            const roomsLeft = this.roomOption.roomsLeft;
            this.maxValue = AccommodationsUtilsService.getMaxRoomSelectionNumber(
                { personCount, roomsLeft },
                this.maxRoomSelectionNumber
            );
        }
    }

    ngOnInit() {
        this.maxValue = Math.min(this.maxRoomSelectionNumber, this.roomOption.roomsLeft);
    }

    public roomCapacity(capacity: number) {
        return new Array(capacity);
    }

    public onSelectRoomOffer(element: string): void {
        this.scrollToElement(element);

        const roomsLeft = this.roomOption.roomsLeft;
        this.maxValue = Math.min(this.maxRoomSelectionNumber, roomsLeft - this.roomOption.personCount);

        if ((roomsLeft === 1 || this.maxValue === 0) && this.roomOption.personCount === 1) {
            this.roomOption.personCount = 0;
            this.roomCountChanged.emit();
            this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOption.id });
            return;
        }

        const maxRoomSelectionNumber = this.maxValue + this.roomOption.personCount;
        const personCount = this.roomOption.personCount;
        if (maxRoomSelectionNumber > 1 && (this.maxValue || personCount) && roomsLeft + personCount > 1) {
            this.openRoomPicker(this.maxValue, element);
            return;
        }

        if (this.maxValue > 0) {
            if (this.roomOption.personCount === 0) {
                this.roomOption.personCount = 1;
                this.roomCountChanged.emit();
                this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOption.id });
            }
        }
    }

    public onOpenTermsModal(): void {
        this.openTermsModal.emit();
    }

    private openRoomPicker(maxRoomSelectionNumber: number, btnElement: string) {
        if (this.roomOption.roomsLeft > this.maxValue) {
            maxRoomSelectionNumber = maxRoomSelectionNumber + this.roomOption.personCount;
        }

        const wheels: MobPickerWheels[] = [];

        for (let i = maxRoomSelectionNumber; i > 0; i--) {
            wheels.push({ display: i + '', value: i + '' });
        }

        if (!wheels.length) {
            wheels.push({ display: '1', value: '1' });
        }

        const selected = this.roomOption.personCount ? [this.roomOption.personCount] : [1];
        this.modalService
            .openPickerModal({ wheels, selected }, { cancelBtnText: 'GENERAL.Remove', title: 'PICKER.SELECT_ROOM' })
            .then((modal) => {
                if (modal.data?.selected) {
                    this.roomOption.personCount = parseInt(modal.data.selected[0], 10);
                    this.roomCountChanged.emit();
                    this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOption.id });

                    this.scrollToElement(btnElement);
                }

                if (modal.data?.cancel) {
                    this.roomOption.personCount = 0;
                    this.roomCountChanged.emit();
                    this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOption.id });
                }
            });
    }

    private scrollToElement(element: string): void {
        requestAnimationFrame(() => {
            this.scrollService.scrollToVisibleArea(element, 'accommodationsRoomsFooter', {
                scrollTop: 16,
                includeElementsHeight: ['mobMainModalHeader'],
            });
        });
    }
}
