import { Component, OnInit } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { Observable } from 'rxjs';
import { Room } from '@schemas/modules/accommodations/room';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { ServiceId } from '@core/constants/service-id.const';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { ModalService } from '@services/modal.service';
// eslint-disable-next-line max-len
import { MobAccommodationAmenitiesComponent } from '../../../accommodation/components/mob-accommodation-amenities/mob-accommodation-amenities.component';
import { BookingSummary } from '@schemas/modules/bookings/booking-summary';

@Component({
    selector: 'tz-mob-selected-accommodation-details',
    templateUrl: './mob-selected-accommodation-details.component.html',
    styleUrls: ['./mob-selected-accommodation-details.component.scss'],
})
export class MobSelectedAccommodationDetailsComponent implements OnInit {
    public accommodationCartData!: Accommodation;
    public selectedHotel!: ICartItem;
    public cart!: any;
    public accommodationReviews$!: Observable<AccommodationReviews>;
    public selectedRoomsData: { rooms: Room[]; options: { [key: number]: ICartSelectedItemBookingOption[] } } = {
        rooms: [],
        options: {},
    };
    private data!: {
        accommodationCartData: Accommodation;
        selectedRooms: { rooms: Room[]; options: { [key: number]: ICartSelectedItemBookingOption[] } };
        bookingSummary: BookingSummary;
    };

    constructor(
        private accommodationsDataService: AccommodationsDataService,
        private cartService: CartService,
        private modalService: ModalService
    ) {}

    public ngOnInit(): void {
        this.initData();
    }

    public showAmenities(): void {
        this.modalService.createMainModal(MobAccommodationAmenitiesComponent, {
            data: this.accommodationCartData.amenityGroups,
            title: 'HOTELS.AMENITIES',
        });
    }

    public showPropertyPolicies(): void {}

    public importantInformation(): void {}

    private initData(): void {
        this.accommodationCartData = this.data.accommodationCartData;
        this.accommodationReviews$ = this.accommodationsDataService.getAccommodationReviews(this.accommodationCartData);
        this.selectedRoomsData = this.data.selectedRooms;
        this.cart = this.data.bookingSummary.cart;

        this.selectedHotel = this.cartService
            .getCartData(ServiceId.ACC, this.cart)
            .items.find((service: ICartItem) => service.dataType === 'hotel');
    }
}
