import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
} from '@angular/core';

import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { ServicePrices } from '@schemas/modules/core/service-prices';
import { UserService } from '@services/modules/user/services/user.service';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-accommodation-room-option',
    templateUrl: './accommodation-room-option.component.html',
    styleUrls: ['./accommodation-room-option.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccommodationRoomOptionComponent implements OnInit, OnChanges {
    @Input() isUserLogged!: boolean;
    @Input() roomOption!: RoomOption;
    @Input() roomPrice!: ServicePrices;
    @Input() cartAmountOfTheRoom = 0;
    @Input() selectedAmountOfTheRoom = 0;
    @Input() count = 0;
    @Input() disable = false;
    @Input() roomsLeftForSelection = 0;
    @Input() bookedOffers = 0;
    @Input() roomType = '';
    @Input() fromSelected = false;
    @Output() optionSelected: EventEmitter<number> = new EventEmitter<number>();
    @Output() roomOptionChange: EventEmitter<RoomOption> = new EventEmitter<RoomOption>();
    @Output() roomMaxSelectedCountReached: EventEmitter<void> = new EventEmitter<void>();
    @Output() openTermsModal: EventEmitter<void> = new EventEmitter<void>();
    public maxValue = 0;

    constructor(public userService: UserService, public platformService: PlatformService) {}

    ngOnInit() {
        this.setMaxAvailableRooms();
        this.roomOption.personCount = this.selectedAmountOfTheRoom || this.cartAmountOfTheRoom || this.count;
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes['roomsLeftForSelection']) {
            this.setMaxAvailableRooms();
        }

        if (changes['count'] && !this.roomOption.personCount) {
            this.roomOption.personCount = this.count;
        }
    }

    public onCountChanged($event: number): void {
        this.roomOption.personCount = $event;
        this.roomOptionChange.emit(this.roomOption);
        this.optionSelected.emit($event);
    }

    public checkShowTerms(roomOption: RoomOption): boolean {
        return AccommodationsUtilsService.checkShowTerms(roomOption, this.isUserLogged);
    }

    public roomCapacity(capacity: number) {
        return new Array(capacity);
    }

    public onRoomMaxSelectedCountReached(): void {
        if (this.roomOption.personCount !== this.roomOption.roomsLeft) {
            this.roomMaxSelectedCountReached.emit();
        }
    }

    public onOpenTermsModal(): void {
        this.openTermsModal.emit();
    }

    private setMaxAvailableRooms(): void {
        const personCount = this.roomOption.personCount;
        const roomsLeft = this.roomOption.roomsLeft;
        this.maxValue = AccommodationsUtilsService.getMaxRoomSelectionNumber(
            { personCount, roomsLeft },
            this.roomsLeftForSelection
        );
    }
}
