import { Component, OnInit } from '@angular/core';
import { Room } from '@schemas/modules/accommodations/room';
import { AccommodationRatings } from '@schemas/modules/accommodations/accommodation-ratings.model';
import { ModalService } from '@services/modal.service';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';
import { Subject } from 'rxjs';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { RecommendedTzRooms } from '@schemas/modules/accommodations/accommodation';
import { UserService } from '@services/modules/user/services/user.service';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { AccommodationDetailsUtilService } from '../../../accommodation-details-util.service';
import { CurrencyService } from '@services/services/currency-service';

@Component({
    selector: 'tz-mob-accommodation-rooms',
    templateUrl: './mob-accommodation-rooms.component.html',
    styleUrls: ['./mob-accommodation-rooms.component.scss'],
})
export class MobAccommodationRoomsComponent implements OnInit {
    public data!: {
        accommodationRooms: Room[];
        accommodation: Accommodation;
        roomsLeftForSelection: number;
        isUserLogged: boolean;
        selectedRooms?: { rooms: Room[]; options: { [key: number]: ICartSelectedItemBookingOption[] } };
        ratings: AccommodationRatings;
        numberOfNights: number;
        openTermsModal$: Subject<RoomOption>;
    };
    public selectRecommended = false;
    public roomsLeftForSelection = 0;
    public roomsQuantity = 0;
    public totalPrice = 0;
    public currency!: string;
    public accommodation!: Accommodation;
    private servicePrices: { price: number; id: string }[] = [];

    constructor(
        private modalService: ModalService,
        private userService: UserService,
        private accommodationDetailsUtilService: AccommodationDetailsUtilService,
        private currencyService: CurrencyService
    ) {}

    ngOnInit() {
        this.roomCountChanged();
        this.accommodation = this.data.accommodation;
        this.selectRecommended = this.accommodationDetailsUtilService.selectRecommended;

        if (this.data.selectedRooms) {
            this.totalPrice = Object.values(this.data.selectedRooms.options)
                .flat()
                .reduce((accumulator, value) => accumulator + value.itemPrice.listPrice, 0);

            if (!this.selectRecommended) {
                this.servicePrices = Object.values(this.data.selectedRooms.options)
                    .flat()
                    .map((e) => ({ price: e.itemPrice.listPrice, id: e.selectedRoom.roomOfferId }));
            } else {
                this.accommodation.rooms.map((room) => room.roomOffers.map((e) => (e.personCount = 0)));
            }
        }
        this.currencyService.selectedCurrencySubject.subscribe((currency) => {
            this.currency = currency.symbol;
        });
    }

    public onBooking(): void {
        this.accommodationDetailsUtilService.selectRecommended = this.selectRecommended;

        if (this.selectRecommended) {
            this.addRecommendedRoomToCart();
        }

        if (!this.data?.selectedRooms) {
            this.modalService.dismiss(true);
        } else {
            this.accommodationDetailsUtilService.addAccommodationItemToCart(this.accommodation);
        }
    }

    public roomCountChanged(): void {
        this.roomsQuantity = AccommodationsUtilsService.getSelectedRoomQuantity(this.data.accommodation.rooms);
        this.roomsLeftForSelection = this.data.roomsLeftForSelection - this.roomsQuantity;
    }

    public openTermsModal(roomOffer: RoomOption): void {
        this.data.openTermsModal$.next(roomOffer);
    }

    getRecommendedRoomPrice(servicePrices: { price: number; recommendedRooms: RecommendedTzRooms[] }) {
        this.data.accommodation.rooms.map((el) => el.roomOffers.map((e) => (e.personCount = 0)));
        this.servicePrices = [];

        this.selectRecommended = true;
        this.roomCountChanged();

        this.totalPrice = servicePrices.price;

        this.roomsQuantity = servicePrices.recommendedRooms.length;
    }

    addRecommendedRoomToCart() {
        const recommendedRooms: { [key: string]: number } = {};
        let roomLeftToFind = this.data.accommodation.numberOfRooms;

        let recOffers = this.data.accommodation.recommendedRooms;
        if (this.userService.IsLogged() && this.data.accommodation.recOffersLoggedIn) {
            recOffers = this.data.accommodation.recRoomsLoggedIn;
        }

        recOffers.forEach((r: { offerId: string | number; allotment: number }) => {
            recommendedRooms[r.offerId] = r.allotment;
        });

        this.data.accommodation.rooms.some((room: { roomOffers: { id: PropertyKey; personCount: number }[] }) => {
            room.roomOffers.some((offer: { id: any; personCount: number }) => {
                if (recommendedRooms.hasOwnProperty(offer.id)) {
                    const allotment = recommendedRooms[offer.id];
                    offer.personCount = allotment;
                    roomLeftToFind--;
                }

                return roomLeftToFind === 0;
            });
            return roomLeftToFind === 0;
        });
    }

    getRoomPrice(servicePrices: { price: number; id: string }) {
        if (this.servicePrices?.find((item) => item.id === servicePrices.id)) {
            this.servicePrices = this.servicePrices.filter((value) => value.id !== servicePrices.id);
        } else {
            this.servicePrices.push(servicePrices);
        }

        this.selectRecommended = false;
        this.totalPrice = this.servicePrices.reduce((accumulator, value) => accumulator + value.price, 0);
    }
}
