import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-service-booking-confirmation',
    templateUrl: './service-booking-confirmation.component.html',
    styleUrls: ['./service-booking-confirmation.component.scss'],
})
export class ServiceBookingConfirmationComponent implements OnInit {
    @Input() hideCancelBtn = false;
    @Output()
    cancelSelection: EventEmitter<void> = new EventEmitter<void>();
    @Output()
    submitBooking: EventEmitter<void> = new EventEmitter<void>();

    constructor(public platformService: PlatformService) {}

    ngOnInit() {}

    public onCancelSelection(): void {
        this.cancelSelection.emit();
    }

    public onSubmitBooking(): void {
        this.submitBooking.emit();
    }
}
