import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
} from '@angular/core';
import { Route } from '@schemas/modules/trains/train-route';
import { TrainClass } from '@schemas/modules/trains/train-class';
import { TravelingLocationInfo } from '@schemas/modules/trains/train-location';
import { TrainSelection } from '@schemas/modules/trains/train-search-query-params';

@Component({
    selector: 'tz-trains-class',
    templateUrl: './trains-class.component.html',
    styleUrls: ['./trains-class.component.scss'],
})
export class TrainsClassComponent implements OnChanges {
    @Input() departure!: TravelingLocationInfo;
    @Input() arrival!: TravelingLocationInfo;
    @Input() trainClasses: TrainClass[] = [];
    @Input() selectedClassId!: string;
    @Input() toggle!: boolean;
    @Input() selectedTrain!: TrainSelection;

    @Output() toggleChange: EventEmitter<boolean> = new EventEmitter();
    @Output() trainClassSelected: EventEmitter<TrainClass> = new EventEmitter();
    @Output() trainClassToggle: EventEmitter<void> = new EventEmitter();

    public stops: Route[] = [];

    constructor() {}

    get isSameStation() {
        if (!this.stops.length) {
            return true;
        }

        return this.stops[0].arrival.location.code === this.stops[1].departure.location.code;
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['trainClasses']) {
            this.setSelectedId();
            this.stops = this.trainClasses[0].route;
        }
    }

    public toggleTicket(event: any): void {
        event.stopPropagation();
        this.toggleChange.emit();
    }

    public toggleSubClass(event: any, selectedClass: TrainClass): void {
        event.stopPropagation();
        this.selectedClassId = selectedClass.id;
    }

    public openTrainTerms(event: any, trainSubClass: TrainClass[]): void {}

    public selectTrainClass(trainClass: TrainClass): void {
        if (trainClass.id !== this.selectedTrain.classId) {
            this.trainClassSelected.emit(trainClass);
        }
    }

    private setSelectedId(): void {
        if (!this.selectedTrain) {
            this.selectedClassId = this.trainClasses[0].id;
            return;
        }

        const hasSelectedTrainClass = this.trainClasses.some((tc: TrainClass) => tc.id === this.selectedTrain.classId);
        if (hasSelectedTrainClass) {
            this.selectedClassId = this.selectedTrain.classId || '';
        }
    }
}
