import {
    Component,
    Input,
    OnInit,
    ChangeDetectorRef,
    Output,
    EventEmitter,
    ChangeDetectionStrategy,
} from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { AccGalleryRes, Gallery } from '@schemas/modules/accommodations/accommodation-gallery.model';
import { DialogService } from '@services/dialog.service';
import { ModalService } from '@services/modal.service';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { PlatformService } from '@services/platform.service';
import { AccommodationGalleryComponent } from '../accommodation-gallery/accommodation-gallery.component';
import { MobAccommodationGalleryComponent } from '../mob-accommodation-gallery/mob-accommodation-gallery.component';
import { MobAccommodationImagesCaroselComponent } from '../mob-accommodation-images-carosel/mob-accommodation-images-carosel.component';

const TOPIMGCOUNT = 4;
const MOBTOPIMGCOUNT = 5;

@Component({
    selector: 'tz-accommodation-image',
    templateUrl: './accommodation-image.component.html',
    styleUrls: ['./accommodation-image.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccommodationImageComponent implements OnInit {
    @Input() giataId!: string;
    @Input() code!: string;
    @Input() imageProvider!: string;
    @Input() hotelName = '';

    @Output() showRooms = new EventEmitter<void>();

    public imagesCount = 0;
    public testImages: Gallery[] = [];
    public loading = true;
    public hotelTopImages: Gallery[] = [];

    private images: Gallery[] = [];
    private testCount = 0;
    private testedImagesCount = 0;
    private selectedImgIds: number[] = [];
    private selectedImgs: string[] = [];
    private maxImgCount = TOPIMGCOUNT;

    constructor(
        private dialogService: DialogService,
        private accommodationsService: AccommodationsDataService,
        private readonly changeDetectorRef: ChangeDetectorRef,
        public platformService: PlatformService,
        private modalService: ModalService
    ) {
        if (this.platformService.isMobileDevice) {
            this.maxImgCount = MOBTOPIMGCOUNT;
        }
    }

    ngOnInit() {
        this.fetchImages();
    }

    public imgCallback(data: any): void {
        this.testedImagesCount++;
        if (data.status === 200) {
            this.selectedImgIds.push(data.id);
            this.selectedImgs[data.id as number] = data.url as string;
            if (this.selectedImgIds.length === this.maxImgCount) {
                this.formTopImages();
            }
            return;
        }

        if (this.images.length < this.testCount) {
            if (this.testCount === this.testedImagesCount) {
                this.formTopImages();
            }
            return;
        }

        this.testCount++;
        this.testImages.push(this.images[this.testCount]);
    }

    public openGallery(selectedIdx: number): void {
        if (this.platformService.isMobileDevice) {
            this.modalService
                .createDetailsPage(MobAccommodationGalleryComponent, {
                    data: {
                        name: this.hotelName,
                        imageProvider: this.imageProvider,
                        images: this.images,
                    },
                    title: this.hotelName,
                })
                .then((modal) => {
                    if (!modal.data) {
                        return;
                    }

                    if (modal.data.showRooms) {
                        this.showRooms.emit();
                    } else if (modal.data.hasOwnProperty('imageId')) {
                        this.openMobImageSlider(modal.data.imageId);
                    }
                });

            return;
        }

        const galleryConf = {
            height: '100%',
            width: '100%',
            maxWidth: '100vw',
            panelClass: 'tz-accommodation-gallery',
            data: {
                name: this.hotelName,
                giataId: this.giataId,
                code: this.code,
                imageProvider: this.imageProvider,
                images: this.images,
                selectedIdx,
            },
        };

        this.dialogService.openAccommodationDialog(AccommodationGalleryComponent, galleryConf);
    }

    private openMobImageSlider(selectedIdx: number): void {
        this.modalService.createMainModal(MobAccommodationImagesCaroselComponent, {
            data: {
                name: this.hotelName,
                imageProvider: this.imageProvider,
                images: this.images,
                selectedIdx,
            },
            title: this.hotelName,
        });
    }

    private formTopImages(): void {
        let count = 0;

        this.images.sort((a: any, b: any) => a.ordinal - b.ordinal);

        for (const img of this.images) {
            if (count === this.maxImgCount) {
                continue;
            }

            if (this.selectedImgIds.indexOf(img.id) > -1) {
                img.url = this.selectedImgs[Number(img.id)];
                this.hotelTopImages.push(img);
                count++;
            }
        }

        if (count < this.maxImgCount) {
            this.imagesCount = count;
        }

        for (let i = count; i < this.maxImgCount; i++) {
            this.hotelTopImages.push({
                id: i,
                url: 'assets/icons/web/img/corrupted.png',
            } as Gallery);
        }

        this.loading = false;
    }

    private initImages(count: number): void {
        if (this.images) {
            this.testImages = ToolsService.deepCopy(this.images.slice(0, this.maxImgCount));
        }
        this.testCount = this.maxImgCount;
        this.imagesCount = count;
        this.changeDetectorRef.detectChanges();
    }

    private fetchImages(): void {
        const galleyRes$ = this.accommodationsService.getHotelImages(this.giataId, this.code, this.imageProvider);
        galleyRes$.subscribe(
            (accGalleryRes: AccGalleryRes) => {
                accGalleryRes.gallery?.forEach((g: Gallery, idx: number) => (g.id = idx));
                this.images = accGalleryRes.gallery;
                this.initImages(accGalleryRes.count);
            },
            (err: any) => {
                this.loading = false;
                console.log('Error in get giata images = ', err);
            }
        );
    }
}
