import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ModalOpenOptions } from '@core/constants/modal';
import { CarRental, RentalTerm } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { ModalService } from '@services/modal.service';
import { CarRentalDataService } from '@services/modules/car-rental/dataservices/car-rental.data.service';
import { Subject } from 'rxjs';
import { CarRentalLocationDialogComponent } from '../car-rental-location-dialog/car-rental-location-dialog.component';
import { CarRentalPackageDialogComponent } from '../car-rental-package-dialog/car-rental-package-dialog.component';
import { MobCarLocationComponent } from '../mob-car-location/mob-car-location.component';
import { MobCarTermsComponent } from '../mob-car-terms/mob-car-terms.component';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-mob-car-rental-details',
    templateUrl: './mob-car-rental-details.component.html',
    styleUrls: ['./mob-car-rental-details.component.scss'],
})
export class MobCarRentalDetailsComponent implements OnInit {
    public carRental!: CarRental;
    public selectedRentalPackage = false;
    private data!: {
        carRental: CarRental;
        selectedCar: CarRental;
        type: 'details' | 'action';
        openPopupModel$: Subject<{ carRental: CarRental; modelType: string }>;
    };

    constructor(
        private modalService: ModalService,
        private carRentalService: CarRentalDataService,
        public platformService: PlatformService
    ) {}

    get isTypeAction(): boolean {
        return this.data.type !== 'details';
    }

    ngOnInit() {
        this.carRental = this.data.carRental;
    }

    public onOpenPopup(modelType: string): void {
        const selectedPackage: any = this.carRental?.packagesAvailable.find(
            (availPackage) => availPackage.name === this.carRental?.currentPackage.name
        );

        switch (modelType) {
            case 'location':
                this.modalService.createDetailsPage(
                    MobCarLocationComponent,
                    {
                        data: { carRental: this.carRental },
                        title: 'CAR_RENTAL.LOCATION',
                    },
                    { openOptions: ModalOpenOptions.LEFT }
                );
                break;
            case 'terms':
                this.carRentalService.getCarRentalTerms(this.carRental, selectedPackage).subscribe(
                    (terms: RentalTerm[]) => {
                        this.showRentalTerms(terms);
                    },
                    (err: any) => {
                        console.log('Terms error: ', err);
                        this.showRentalTerms([]);
                    }
                );
                break;
            case 'package':
                this.modalService.createMainModal(
                    CarRentalPackageDialogComponent,
                    {
                        data: { selectedPackage },
                        title: 'CAR_RENTAL.PACKAGE',
                        cssClass: 'mob-modal-fullscreen',
                    },
                    { openOptions: ModalOpenOptions.RIGHT }
                );
                break;
            default:
                break;
        }
    }

    public packageSelected(isPackageSelected: boolean): void {
        this.selectedRentalPackage = isPackageSelected;
    }

    public selectRentalPackage(carRentalPackage: CarRentalPackage): void {
        this.carRental.currentPackage = carRentalPackage;
    }

    public onConfirm(): void {
        this.modalService.dismiss(this.carRental);
    }

    private showRentalTerms(carRentalTerms: RentalTerm[]): void {
        this.modalService.createDetailsPage(
            MobCarTermsComponent,
            {
                data: { carRentalTerms },
                title: 'CAR_RENTAL.TERMS_CONDITIONS',
            },
            { openOptions: ModalOpenOptions.LEFT }
        );
    }
}
