import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { BestValType } from '@schemas/modules/search/search';
import { ResultsTools } from '@services/modules/results/services/tools.shared.services';
import { PlatformService } from '@services/platform.service';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-flight-list-item',
    templateUrl: './flight-list-item.component.html',
    styleUrls: ['./flight-list-item.component.scss'],
})
export class FlightListItemComponent implements OnInit {
    @Input() twoWayFlight = false;
    @Input() flight!: Flight;
    @Input() onWardSelected = false;
    @Input() returnSelected = false;
    @Input() selectable = true;
    @Input() oldPrice = 0;
    @Input() totalPassengers = 0;

    @Output() selectedOnwardFlight: EventEmitter<boolean> = new EventEmitter<boolean>();
    @Output() selectedReturnFlight: EventEmitter<boolean> = new EventEmitter<boolean>();

    public bestValType: BestValType | undefined;

    constructor(public platformService: PlatformService) {}

    ngOnInit(): void {
        if (this.flight && this.flight.bestValType) {
            this.bestValType = ResultsTools.getBestValType(this.flight.bestValType, ServiceId.FLIGHT);
        }
    }

    public onOnwardFlightChanged(selected: boolean) {
        this.selectedOnwardFlight.emit(selected);
    }

    public onReturnFlightChanged(selected: boolean) {
        this.selectedReturnFlight.emit(selected);
    }
}
