import { Component, EventEmitter, Input, Output } from '@angular/core';

import { FilterShowMoreOptions, FiltersShowMoreOptions } from '@schemas/modules/core/filters/service-filter';

@Component({
    selector: 'tz-filter-show-more-less-button',
    templateUrl: './filter-show-more-less-button.component.html',
    styleUrls: ['./filter-show-more-less-button.component.scss'],
})
export class FilterShowMoreLessButtonComponent {
    @Input() limitTo!: number;
    @Input() filterId!: string;
    @Input() itemsCount!: number;
    @Input() filtersOptions!: FiltersShowMoreOptions;

    @Output() showMore: EventEmitter<FilterShowMoreOptions> = new EventEmitter<FilterShowMoreOptions>();
    @Output() showLess: EventEmitter<FilterShowMoreOptions> = new EventEmitter<FilterShowMoreOptions>();
    @Output() showAll: EventEmitter<FilterShowMoreOptions> = new EventEmitter<FilterShowMoreOptions>();

    public onShowMore(): void {
        this.showMore.emit(this.getFiltersShowMoreOptions());
    }

    public onShowLess(): void {
        this.showLess.emit(this.getFiltersShowMoreOptions());
    }

    public onShowAll(): void {
        this.showAll.emit(this.getFiltersShowMoreOptions());
    }

    private getFiltersShowMoreOptions(): FilterShowMoreOptions {
        return {
            id: this.filterId,
            itemsCount: this.itemsCount,
            limitTo: this.limitTo,
        };
    }
}
