import { ChangeDetectionStrategy, Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FlightLuggageOption } from '@schemas/modules/flights/flight-luggage.model';

@Component({
    selector: 'tz-flight-bag-separator',
    templateUrl: './flight-bag-separator.component.html',
    styleUrls: ['./flight-bag-separator.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FlightBagSeparatorComponent implements OnChanges {
    @Input() luggageOption!: FlightLuggageOption;
    @Input() componentType: 'details' | 'package' = 'details';
    public displaySeparator = false;

    constructor() {}

    ngOnChanges(changes: SimpleChanges) {
        const luggageOption = changes['luggageOption'].currentValue;
        this.displaySeparator = !!(
            luggageOption.dimensions &&
            luggageOption.weightTexts.length &&
            luggageOption.dimensions?.length + luggageOption.weightTexts?.join(', ').length <
                (this.componentType === 'package' ? 40 : 23)
        );
    }
}
