import {
    AfterViewInit,
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';

import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
import { Flight } from '@schemas/modules/flights/flight';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { IonAccordion, IonAccordionGroup } from '@ionic/angular';
import { CombinationId } from '@schemas/modules/core/base-types';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { TzSelectedService } from '../../../../selectedUtils/tz-selected-service';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { PlatformService } from '@services/platform.service';
import { FlightsService } from '@services/modules/transport/flights/flights.service';
import { ModalService } from '@services/modal.service';
import { MobFlightDetailsComponent } from '../mob-flight-details/mob-flight-details.component';
import { PageUtils } from '@core/utils/page-utils';
import { Router } from '@angular/router';
import { PagesUrls } from '@core/constants/pages-urls';

@Component({
    selector: 'tz-selected-flights',
    templateUrl: './selected-flights.component.html',
    styleUrls: ['./selected-flights.component.scss'],
})
export class SelectedFlightsComponent implements OnInit, OnChanges, AfterViewInit {
    @ViewChild('selectedFLightAccordionPanel') selectedFlightAccordionPanel?: IonAccordionGroup;
    @ViewChild('selectedFLightAccordion') selectedFlightAccordion?: IonAccordion;
    @Input() flightCartData!: IFlightsCartData;
    @Input() combId!: CombinationId;
    @Input() serviceList = false;
    @Input() packages: Flight[] | undefined = [];
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeTickets: EventEmitter<void> = new EventEmitter();
    @Output() changeOptions: EventEmitter<Flight> = new EventEmitter();
    @Output() newFlightSelected: EventEmitter<Flight> = new EventEmitter();
    @Output() selectPackage: EventEmitter<{ package: Flight; flightId: string }> = new EventEmitter<{
        package: Flight;
        flightId: string;
    }>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
    @Output() showingPackage: EventEmitter<boolean> = new EventEmitter<boolean>();

    public buttons: string[] = [];
    public confirmable = false;
    public selectedFlight!: Flight;
    public selectedCartItem!: ICartItem;
    public selectedPackageIndex = 0;
    public showPackages = false;
    public selectedFlightOptionId!: string;
    public invalidCartItem = false;
    public totalPassengers = 0;
    public isBasketPage!: boolean;

    constructor(
        public platformService: PlatformService,
        private flightService: FlightsService,
        private modalService: ModalService,
        private router: Router
    ) {}

    ngOnInit() {
        this.formSelectedFlight();
        this.initializeActionBarButtons();
        this.addSpaceInSelectedFlightsSummaryText();
        this.isBasketPage = this.checkIsBasketPage();
    }

    ngAfterViewInit() {
        this.handleExtraParams();
    }

    public toggleShowDetail() {
        if (this.selectedFlightAccordionPanel !== undefined) {
            this.selectedFlightAccordionPanel.value = null;
        }
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes['packages'] && changes['packages'].currentValue) {
            this.getCurrentSelectedPackageIndex();
        }

        if (changes['flightCartData']) {
            this.formSelectedFlight();
        }
    }

    public onRemoveSelected(): void {
        this.removedItem.emit(this.selectedFlightOptionId);
    }

    public onDebugDialogOpened(): void {}

    public onChangeOptions(): void {
        console.log('change options');
        if (!this.platformService.isMobileDevice) {
            (this.selectedFlightAccordionPanel as IonAccordionGroup).value = null;
            (this.selectedFlightAccordion as IonAccordion).value = '';
            this.showPackages = true;
            this.showingPackage.emit(true);
        }
        this.changeOptions.emit(this.selectedFlight);
    }

    public onChangeTickets(): void {
        console.log('change tickets');
        if (!this.platformService.isMobileDevice) {
            (this.selectedFlightAccordionPanel as IonAccordionGroup).value = null;
            (this.selectedFlightAccordion as IonAccordion).value = '';
        }
        this.changeTickets.emit();
    }

    public onCancelFLightPackageChange(): void {
        this.showPackages = false;
        this.selectedFlight = this.selectedCartItem.selectedItem.data;
        this.getCurrentSelectedPackageIndex();
        this.showingPackage.emit(false);
    }

    public onSubmitFlightPackageChange() {
        /*this.showPackages = false;
        this.showingPackage.emit(false);*/
        const flight = this.packages && this.packages[this.selectedPackageIndex];
        this.newFlightSelected.emit(flight);
    }

    public onSelectPackage(index: number) {
        this.selectedPackageIndex = index;
        this.confirmable = !!(this.packages && this.packages[this.selectedPackageIndex].id !== this.selectedFlight.id);
        if (this.packages) {
            this.selectedFlight = this.packages[this.selectedPackageIndex];
        }
    }

    public onCancelTicket(): void {
        this.cancel.emit();
    }

    public onOpenTransportTerms(): void {
        this.flightService.showFlightTrems(this.selectedFlight);
    }

    public onOpenCheckinAllowance(): void {
        this.flightService.showFlightCheckinAllowance(this.selectedFlight.checkinAllowance);
    }

    public onShowFlightDetails(): void {
        this.modalService.createDetailsPage(MobFlightDetailsComponent, {
            data: {
                flight: this.selectedFlight,
                flightsPackages: this.packages,
                type: 'details',
                totalPassengers: this.totalPassengers,
            },
            title: 'FLIGHT.SELECTED',
        });
    }

    private handleExtraParams() {
        const state = history.state;
        if (state && state.extra) {
            for (const extraAction in state.extra) {
                if (state.extra.hasOwnProperty(extraAction)) {
                    switch (extraAction) {
                        case 'changeOptions':
                            console.log('change options');
                            this.onChangeOptions();
                            break;
                    }
                }
            }
        }
    }

    private formSelectedFlight(): void {
        this.selectedCartItem = this.flightCartData.items[0];
        this.selectedFlightOptionId = this.selectedCartItem.selectedItem.bookingOptions[0].optionId;
        this.selectedFlight = this.selectedCartItem.selectedItem.data;

        const travellersData = CartUtils.getTravellersData(this.flightCartData.items);
        if (travellersData) {
            this.totalPassengers = travellersData.total;
        }

        this.invalidCartItem = !this.selectedCartItem.selectedItem.bookingOptions.some(
            (option) =>
                !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
        );
    }

    private getCurrentSelectedPackageIndex(): void {
        const index =
            this.selectedFlight &&
            this.packages &&
            this.packages.findIndex((flightPackage: Flight) => flightPackage.id === this.selectedFlight.id);
        if (index || index === 0) {
            this.selectedPackageIndex = index;
        }
    }

    private initializeActionBarButtons() {
        this.buttons = TzSelectedService.initializeActionBarButtons(
            this.selectedCartItem.selectedItem,
            this.fromBookings,
            this.serviceList,
            this.paymentCaptureMode,
            false
        );

        if (!this.fromBookings && !CartUtils.hasBooked(this.selectedCartItem.selectedItem.bookingOptions)) {
            this.buttons.push(this.totalPassengers > 1 ? 'changeTickets' : 'changeTicket');

            if (this.selectedCartItem.selectedItem.hasUpgrades) {
                if (!CartUtils.hasBookingStarted(this.selectedCartItem.selectedItem.bookingOptions)) {
                    this.buttons.push('changeOptions');
                } else if (CartUtils.hasUnavailable(this.selectedCartItem.selectedItem.bookingOptions)) {
                    this.buttons.push('update');
                }
            }
        }
    }

    private addSpaceInSelectedFlightsSummaryText(): void {
        const splitSummaryText = this.selectedCartItem.summaryTitle?.split(':');
        if (!!splitSummaryText) {
            this.selectedCartItem.summaryTitle = `${splitSummaryText[0]} : ${splitSummaryText[1]}`;
        }
    }

    private checkIsBasketPage(): boolean {
        return PageUtils.is(this.router.url, PagesUrls.BASKET);
    }
}
