import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CarRental, RentalTerm } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { DialogService } from '@services/dialog.service';
import { Observable } from 'rxjs';

import { CarRentalDataService } from '../dataservices/car-rental.data.service';
// eslint-disable-next-line max-len
import { CarRentalTermsDialogComponent } from 'src/app/modules/results/car-rentals/components/car-rental-terms.dialog/car-rental-terms.dialog.component';
// eslint-disable-next-line max-len
import { CarRentalLocationDialogComponent } from 'src/app/modules/results/car-rentals/components/car-rental-location-dialog/car-rental-location-dialog.component';
// eslint-disable-next-line max-len
import { CarRentalPackageDialogComponent } from 'src/app/modules/results/car-rentals/components/car-rental-package-dialog/car-rental-package-dialog.component';
import { MatDialogRef } from '@angular/material/dialog';

@Injectable({
    providedIn: 'root',
})
export class CarRentalService {
    private termsModelRef!: MatDialogRef<CarRentalTermsDialogComponent>;
    constructor(
        private dialogService: DialogService,
        private translateService: TranslateService,
        private carRentalService: CarRentalDataService
    ) {}

    public openPopupModel(rentalModel: { carRental: CarRental; modelType: string }): void {
        const carRental = rentalModel.carRental;
        const popupConf = {
            width: '470px',
            maxHeight: '90vh',
            panelClass: 'tz-rental-dialog',
            data: {} as any,
        };
        const selectedPackage = carRental?.packagesAvailable.find(
            (availPackage) => availPackage.name === carRental?.currentPackage.name
        );

        switch (rentalModel.modelType) {
            case 'terms':
                if (carRental && selectedPackage) {
                    this.opneRentalTerms(carRental, selectedPackage);
                }
                break;
            case 'location':
                this.dialogService.openServiceDialog(CarRentalLocationDialogComponent, {
                    ...popupConf,
                    data: { carRental },
                });
                break;
            case 'package':
                if (selectedPackage) {
                    this.dialogService.openServiceDialog(CarRentalPackageDialogComponent, {
                        ...popupConf,
                        data: { selectedPackage },
                    });
                }
                break;
        }
    }

    private opneRentalTerms(carRental: CarRental, selectedPackage: CarRentalPackage): void {
        const key = selectedPackage.rateQualifier + selectedPackage.productID;
        const openTermsModel = (carRentalTrems: RentalTerm[]) => {
            if (!carRentalTrems || carRentalTrems.length === 0) {
                this.dialogService.openSnackBar({
                    message: this.translateService.instant('NOTIFS.rentalNoTandcs'),
                });
                return;
            }

            if (this.termsModelRef && this.termsModelRef.getState() === 0) {
                console.log('Skipped multiple click');
                return;
            }

            this.termsModelRef = this.dialogService.openServiceDialog(CarRentalTermsDialogComponent, {
                width: undefined,
                maxHeight: '90vh',
                panelClass: 'tz-terms-dialog-large',
                maxWidth: '940px',
                data: { carRentalTrems },
            });
        };

        this.carRentalService.getCarRentalTerms(carRental, selectedPackage).subscribe(
            (terms: RentalTerm[]) => {
                openTermsModel(terms);
            },
            (err: any) => {
                console.log('Terms error: ', err);
                openTermsModel([]);
            }
        );
    }
}
