import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { CarRentalService } from '@services/modules/car-rental/services/car-rental.service';
import { CombinationId } from '@schemas/modules/core/base-types';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { TzSelectedService } from '../../../selectedUtils/tz-selected-service';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { PlatformService } from '@services/platform.service';
import { ModalService } from '@services/modal.service';
import { MobCarRentalDetailsComponent } from '../mob-car-rental-details/mob-car-rental-details.component';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ToolsService } from '@core/utils/tools-service';

@Component({
    selector: 'tz-car-rentals-cart-item',
    templateUrl: './car-rental-cart-item.component.html',
    styleUrls: ['./car-rental-cart-item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CarRentalCartItemComponent implements OnInit, OnDestroy {
    @Input() serviceList = false;
    @Input() combId!: CombinationId;
    @Input() carRentalItem!: ICartItem;
    @Input() packages: CarRentalPackage[] = [];
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    //used to notify selected cart item to display car packages options
    @Input() selectedChangeOptions: EventEmitter<void> = new EventEmitter<void>();
    @Output() showPackagesFlag: EventEmitter<boolean> = new EventEmitter<boolean>();
    @Output() remove: EventEmitter<void> = new EventEmitter<void>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() loadCarRentals: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() changeCarRental: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeOptions: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
    @Output() showCarRentalDetails: EventEmitter<CarRental> = new EventEmitter<CarRental>();
    public buttons: string[] = [];
    //public optionId!: string;
    public showPackages = false;
    public invalidCartItem = false;
    public carRentalItemData: CarRental | undefined;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private carRentalService: CarRentalService,
        public platformService: PlatformService,
        private modalService: ModalService
    ) {}

    ngOnInit() {
        //this.optionId = this.carRentalItem.selectedItem.bookingOptions[0].optionId;
        this.carRentalItemData = ToolsService.deepCopy(this.carRentalItem.selectedItem.data);
        this.initializeActionBarButtons();
        this.selectedChangeOptions.subscribe(() => {
            console.log('external change options called');
            this.onChangeOptions();
        });
        this.invalidCartItem = !this.carRentalItem.selectedItem.bookingOptions.some(
            (option) =>
                !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
        );
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
        this.selectedChangeOptions.unsubscribe();
    }

    public onRemove(): void {
        this.remove.emit();
    }

    public onChangeCarRental(): void {
        this.changeCarRental.emit();
    }

    public onChangeOptions(): void {
        if (this.serviceList) {
            if (this.platformService.isMobileDevice) {
                this.showCarRentalDetails.emit(this.carRentalItemData);
            } else {
                this.setShowPackages(true);
                if (this.carRentalItemData) {
                    this.loadCarRentals.emit(this.carRentalItemData);
                }
            }
        } else {
            this.changeOptions.emit(this.carRentalItemData);
        }
    }

    public onCancel(): void {
        this.cancel.emit();
    }

    public hasChangeOptions(): boolean {
        const bookingOptions = this.carRentalItem.selectedItem.bookingOptions;
        if (bookingOptions && bookingOptions.length) {
            return bookingOptions[0].rentalPackageInfo.nbPackage > 1 || bookingOptions[0].rentalPackageInfo.hasExtras;
        }
        return false;
    }

    public onCarRentalChangeCancel(): void {
        this.carRentalItemData = ToolsService.deepCopy(this.carRentalItem.selectedItem.data);
        this.setShowPackages(false);
    }

    public onSubmitCarRentalChange() {
        this.changeOptions.emit(this.carRentalItemData);
    }

    public selectRentalPackage(rentalPackage: CarRentalPackage): void {
        if (this.carRentalItemData) {
            this.carRentalItemData.currentPackage = ToolsService.deepCopy(rentalPackage);
        }
    }

    public onOpenPopupModel(rentalModel: { carRental: CarRental; modelType: string }) {
        this.carRentalService.openPopupModel(rentalModel);
    }

    public onShowCarDetails(): void {
        const openPopupModel$: Subject<{
            carRental: CarRental;
            modelType: string;
        }> = new Subject<{
            carRental: CarRental;
            modelType: string;
        }>();

        openPopupModel$.pipe(takeUntil(this.destroy$)).subscribe((data) => {
            this.onOpenPopupModel(data);
        });

        this.modalService.createDetailsPage(MobCarRentalDetailsComponent, {
            data: {
                carRental: this.carRentalItem.selectedItem.data,
                openPopupModel$,
                type: 'details',
            },
            title: 'CAR_RENTAL.SELECTED',
        });
    }

    private setShowPackages(val: boolean) {
        this.showPackages = val;
        this.showPackagesFlag.emit(val);
    }

    private initializeActionBarButtons() {
        this.buttons = TzSelectedService.initializeActionBarButtons(
            this.carRentalItem.selectedItem,
            this.fromBookings,
            this.serviceList,
            this.paymentCaptureMode,
            false
        );

        if (!this.fromBookings && !CartUtils.hasBooked(this.carRentalItem.selectedItem.bookingOptions)) {
            this.buttons.push('changeCarRental');

            if (this.hasChangeOptions()) {
                if (!CartUtils.hasBookingStarted(this.carRentalItem.selectedItem.bookingOptions)) {
                    this.buttons.push('changeOptions');
                } else if (CartUtils.hasUnavailable(this.carRentalItem.selectedItem.bookingOptions)) {
                    this.buttons.push('update');
                }
            }
        }
    }
}
