import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { TrainClass } from '@schemas/modules/trains/train-class';
import { CombinationId } from '@schemas/modules/core/base-types';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { PlatformService } from '@services/platform.service';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';

@Component({
    selector: 'tz-train-cart-item',
    templateUrl: './train-cart-item.component.html',
    styleUrls: ['./train-cart-item.component.scss'],
})
export class TrainCartItemComponent implements OnInit, OnChanges {
    @Input() cartItem!: any; // Todo: need to change once search engine is cleaned up with data type
    @Input() combId!: CombinationId;
    @Input() serviceList = false;
    @Input() fromBookings = false;
    @Input() isRoundTrip = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeTickets: EventEmitter<void> = new EventEmitter();
    @Output() changeOptions: EventEmitter<TRAIN> = new EventEmitter();
    @Output() selectedId: EventEmitter<string> = new EventEmitter<string>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();

    public buttons: string[] = [];
    public train!: TrainClass;
    public showDetails = false;
    public trainClasses: TrainClass[] = [];
    public optionId!: string;
    public invalidCartItem = false;
    public totalPassengers = 0;

    constructor(public platformService: PlatformService) {}

    ngOnInit() {
        this.init();

        // Todo: path needs to be done from server
        const oldPath = '/web/assets/img/trainSupplierLogos/';
        const newPath = '/assets/icons/web/img/trainSupplierLogos/';
        this.train.operatorImage = this.train.operatorImage.replace(oldPath, newPath);
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['cartItem']) {
            const travellersData = CartUtils.getTravellersData([this.cartItem]);
            if (travellersData) {
                this.totalPassengers = travellersData.total;
            }
        }
    }

    public toggleDetails() {
        this.showDetails = !this.showDetails;
    }

    public onRemoveSelected(): void {
        this.removedItem.emit(this.optionId);
    }

    public onChangeTickets(): void {
        this.changeTickets.emit();
    }

    public onChangeOptions(): void {
        this.changeOptions.emit();
    }

    public onCancelTicket(): void {
        this.cancel.emit();
    }

    public setSelectedId(itemId: string): void {
        this.selectedId.emit(itemId);
    }

    private init(): void {
        this.optionId = this.cartItem.selectedItem.bookingOptions[0].optionId;
        this.train = this.cartItem.selectedItem.data.onward;
        this.trainClasses = [this.train];

        this.invalidCartItem = !this.cartItem.selectedItem.bookingOptions.some(
            (option: ICartSelectedItemBookingOption) =>
                !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
        );
    }
}
