import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-mob-flight-pricing-plans',
    templateUrl: './mob-flight-pricing-plans.component.html',
    styleUrls: ['./mob-flight-pricing-plans.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobFlightPricingPlansComponent implements OnInit {
    @Input() packages!: Flight;
    @Input() flight!: Flight;
    @Input() totalPassengers!: number;
    @Input() selectedFlight!: Flight | undefined | null;
    @Input() currentIndex!: number;
    @Input() hidePriceInfo = false;
    @Input() isTypeAction = true;
    @Output() selectedFlightChange = new EventEmitter<Flight | null>();
    @Output() openTerms = new EventEmitter<Flight>();

    constructor(private scrollService: ScrollService) {}

    public ngOnInit(): void {}

    public onSelectPackage(flight: Flight, element: string): void {
        requestAnimationFrame(() => {
            this.scrollService.scrollToVisibleArea(element, 'flightDetailsFooter', {
                scrollTop: 15,
                includeElementsHeight: ['mobMainModalHeader'],
            });
        });

        if (this.selectedFlight && this.selectedFlight.id === flight.id) {
            this.selectedFlight = null;
            this.selectedFlightChange.emit(null);
            return;
        }

        if (flight) {
            this.selectedFlight = flight;
            this.selectedFlightChange.emit(flight as Flight);
        }
    }

    public onOpenTermsAndConditions($event: Event): void {
        $event.stopPropagation();
        this.openTerms.emit(this.flight);
    }
}
