import { Component, Input, OnInit } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { TermsAndConditions } from '@schemas/modules/trains/train-terms';

@Component({
    selector: 'tz-mob-train-terms-modal',
    templateUrl: './mob-train-terms-modal.component.html',
    styleUrls: ['./mob-train-terms-modal.component.scss'],
})
export class MobTrainTermsModalComponent implements OnInit {
    @Input() data!: { terms: any; operator: any };
    public terms: TermsAndConditions[] = [];
    public operator!: string;
    public showViaRailTerms = false;

    constructor() {}

    ngOnInit(): void {
        this.operator = ToolsService.deepCopy(this.data.operator);
        this.terms = ToolsService.deepCopy(this.data.terms);
    }

    public trackByFn(index: number) {
        return index;
    }
}
