import { Component, EventEmitter, Input, OnInit, Output, Inject, LOCALE_ID, ViewEncapsulation } from '@angular/core';
import { formatCurrency, formatNumber, getCurrencySymbol } from '@angular/common';
import { formatDate } from '@angular/common';
import { Options } from '@angular-slider/ngx-slider';
import { TranslateService } from '@ngx-translate/core';

import { SettingsService } from '@services/services/settings-service';
import {
    FilterSlider,
    FilterSliderOptionType,
    FilterSliderUnitType,
} from '@schemas/modules/core/filters/service-filter';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-slider',
    templateUrl: './slider.component.html',
    styleUrls: ['./slider.component.scss'],
})
export class SliderComponent implements OnInit {
    @Input() slider!: FilterSlider;
    @Input() displayRange = true;
    @Input() displayRangeCount = false;
    @Input() disabled = false;
    @Input() className = '';
    @Output() minChange: EventEmitter<number> = new EventEmitter<number>();
    @Output() maxChange: EventEmitter<number> = new EventEmitter<number>();
    @Output() sliderChange: EventEmitter<FilterSlider> = new EventEmitter<FilterSlider>();
    options: Options = {};

    public selectionInfo = '';
    public rangeCount = { min: '', max: '' };
    public sliderTypes = FilterSliderOptionType;
    public mobSelectionInfo: string[] = [];

    constructor(
        private translateService: TranslateService,
        private settingsService: SettingsService,
        public platformService: PlatformService
    ) {}

    ngOnInit(): void {
        this.formOptions();
    }

    public onChange() {
        const min = this.slider.min;
        const max = this.slider.max;
        if (min > max) {
            console.error('min > max', min, max);
        }
        this.slider.max = this.slider.selected_max;
        this.slider.min = this.slider.selected_min;
        this.formatSliderText();

        this.slider.max = max;
        this.slider.min = min;
    }

    public onChangeEnd() {
        if (this.slider.selected_max === this.slider.max && this.slider.selected_min === this.slider.min) {
            return;
        }

        this.onChange();
        this.slider.max = this.slider.selected_max;
        this.slider.min = this.slider.selected_min;
        this.slider.selected = true;
        this.sliderChange.emit(this.slider);
    }

    private formOptions(): void {
        this.options = this.slider.options;
        this.options.showSelectionBar = true;
        this.formatSliderText();
    }

    private formatSliderCountRange(min?: string, sep?: string, max?: string, last?: string) {
        this.rangeCount = { min: min || '0', max: max || '' };
        this.selectionInfo = `${min || ''} ${sep || ''} ${max || ''} ${last || ''}`;

        if (this.platformService.isMobileDevice) {
            this.mobSelectionInfo = this.selectionInfo
                .split('|')
                .map((selectionInfoItem: string) => selectionInfoItem.trim());
        }
    }

    // format slider text based on their type
    private formatSliderText() {
        const slider = this.slider;
        if (!slider) {
            return;
        }
        const sep = '';
        switch (slider.unit_type) {
            case FilterSliderUnitType.CURRENCY:
                /** price:: `$ 10.00  $ 12.00` */
                this.formatSliderCountRange(
                    this.platformService.isMobileDevice ? `Range | ` : 'Range ',
                    this.formatCurrency(slider.min, slider.unit),
                    sep,
                    this.formatCurrency(slider.max, slider.unit)
                );
                return;
            case FilterSliderUnitType.DISTANCE:
                /**  distance:: `100 km`| `10  100 km` */
                if (slider.slider_type === FilterSliderOptionType.SINGLE) {
                    this.formatSliderCountRange(
                        undefined,
                        undefined,
                        this.formatNumber(slider.max),
                        slider.unit || 'km'
                    );
                    return;
                }
                this.formatSliderCountRange(
                    this.platformService.isMobileDevice
                        ? `Range | ${this.formatNumber(slider.min)}`
                        : this.formatNumber(slider.min),
                    sep,
                    this.formatNumber(slider.max),
                    slider.unit || 'km'
                );
                return;
            case FilterSliderUnitType.DATE:
                /**  date::
                 * 1. for different date (Thu 10:22  Fri 04:33),
                 * 2. for different date with more than one week (17 Thu 10:22  24 Thu 04:33),
                 * 3. for same date (Thu 10:22  04:33)
                 */
                const timezone = this.slider.conversion_unit || 'UTC';
                const timeFormat = this.settingsService.locales.DATETIME_FORMATS.shortTime;
                let t = this.platformService.isMobileDevice ? 'EEE | ' + timeFormat : 'EEE ' + timeFormat;
                let f = t;
                const from = this.getDate(this.slider.min * 1000);
                const to = this.getDate(this.slider.max * 1000);
                if (from.date === to.date && from.month === to.month && from.yr === to.yr) {
                    t = timeFormat;
                }
                if (from.yr !== to.yr || from.month !== to.month || to.date - from.date > 6) {
                    f = t = this.platformService.isMobileDevice ? 'dd EEE | ' + timeFormat : 'dd EEE ' + timeFormat;
                }
                const minDate = this.formatDate(this.slider.min, f, timezone);
                const maxDate = this.formatDate(this.slider.max, t, timezone);
                this.formatSliderCountRange(minDate, sep, maxDate);
                return;
            case FilterSliderUnitType.DURATION:
                /** duration:: `00h 00m  01h 10m`*/
                this.formatSliderCountRange(this.formatDuration(slider.min), sep, this.formatDuration(slider.max));
                return;
            default:
                /** default `10  100` */
                if (this.platformService.isMobileDevice) {
                    this.formatSliderCountRange(
                        'Range |',
                        this.formatNumber(slider.min),
                        sep,
                        this.formatNumber(slider.max)
                    );
                } else {
                    this.formatSliderCountRange(this.formatNumber(slider.min), sep, this.formatNumber(slider.max));
                }
                return;
        }
    }

    // format slider text
    // format duration formats the slider duration number to readable text
    private formatDuration(duration: number) {
        const hr = Math.floor(duration / 60);
        const min = duration % 60;
        const f = (num: number) => (num > -1 && num < 10 ? `${num}` : `${num}`);
        if (hr > 0) {
            if (min > 0) {
                return `${f(hr)}h ${f(min)}m`;
            }
            return `${f(hr)}h`;
        }
        return `${f(min)}m`;
    }

    private formatCurrency(value: number, curCode: string) {
        return formatCurrency(
            value,
            this.settingsService.langCode,
            getCurrencySymbol(curCode, 'wide'),
            curCode,
            '1.0-0'
        );
    }

    private formatNumber(value: number) {
        return formatNumber(value, this.settingsService.langCode, '1.1');
    }

    private formatDate(date: number, format: string, timezone: string): string {
        return formatDate(new Date(date * 1000), format, this.settingsService.langCode, timezone);
    }

    private getDate(d: number): { date: number; month: number; yr: number } {
        const fromDate = new Date(d);
        return {
            date: fromDate.getUTCDate(),
            month: fromDate.getUTCMonth(),
            yr: fromDate.getUTCFullYear(),
        };
    }
}
