import { Component, Inject, Input, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { RentalTerm } from '@schemas/modules/car-rentals/car-rental';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-car-rental-terms.dialog',
    templateUrl: './car-rental-terms.dialog.component.html',
    styleUrls: ['./car-rental-terms.dialog.component.scss'],
})
export class CarRentalTermsDialogComponent implements OnInit {
    @Input() carRentalTerms: RentalTerm[] = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<CarRentalTermsDialogComponent>,
        private dialogService: DialogService
    ) {
        this.carRentalTerms = ToolsService.deepCopy(data.carRentalTrems);
        console.log('Trems: ', this.carRentalTerms);
    }

    ngOnInit() {}

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }
}
