import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { TzServiceTabs } from '../../../tabsUtils/tz-tabs-service';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-mob-transfer-list-item',
    templateUrl: './mob-transfer-list-item.component.html',
    styleUrls: ['./mob-transfer-list-item.component.scss'],
})
export class MobTransferListItemComponent implements OnInit {
    @Input() transfer: Transfer | null | undefined;
    @Input() selectable = true;
    @Input() cancelable = false;
    @Input() oldPrice = 0;
    @Input() totalPassengers = 0;
    @Input() isSummary = true;
    @Output() selectedTransfer: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancelSelection: EventEmitter<void> = new EventEmitter<void>();

    public changedPriceDiff = '';

    constructor(private serviceTabs: TzServiceTabs, private scrollService: ScrollService) {}

    public ngOnInit(): void {
        this.initData();
    }

    public onSelectTransfer(element: string): void {
        this.selectedTransfer.emit();

        requestAnimationFrame(() => {
            this.scrollService.scrollToVisibleArea(element, 'transferDetailsFooter', {
                scrollTop: 16,
            });
        });
    }

    public onCancelSelection(): void {
        this.cancelSelection.emit();
    }

    private initData(): void {
        if (this.oldPrice > 0 && this.transfer?.prices?.listPrice) {
            this.changedPriceDiff = this.serviceTabs.updatePriceChanging(
                this.oldPrice,
                this.transfer?.prices?.listPrice
            );
        }
    }
}
