import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { ToolsService } from '@core/utils/tools-service';
import { Gallery } from '@schemas/modules/accommodations/accommodation-gallery.model';
const paginationCount = 7;
@Component({
    selector: 'tz-mob-accommodation-images-carosel',
    templateUrl: './mob-accommodation-images-carosel.component.html',
    styleUrls: ['./mob-accommodation-images-carosel.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccommodationImagesCaroselComponent implements OnInit {
    public data!: {
        name: string;
        imageProvider: string;
        images: Gallery[];
        selectedIdx: number;
    };

    public images: Gallery[] = [];
    public currentImg!: Gallery;
    public currentImgIdx = -1;

    private loadImages: Gallery[] = [];
    private loadedImgCount = 0;

    constructor() {}

    get currentPagination(): number[] {
        let maxImages = paginationCount;
        let activeIdx = this.currentImgIdx;

        const totalImages = this.images.length;
        const remainingCount = totalImages - activeIdx;

        if (totalImages < maxImages) {
            maxImages = totalImages;
        }

        if (remainingCount < paginationCount) {
            maxImages = totalImages % paginationCount;
            activeIdx = maxImages - remainingCount;
        } else if (activeIdx + 1 > paginationCount) {
            activeIdx %= paginationCount;
        }

        return Array(maxImages)
            .fill(0)
            .map((x, i) => {
                if (activeIdx === i) {
                    return 1;
                }

                return 0;
            });
    }

    ngOnInit() {
        this.currentImgIdx = this.data.selectedIdx;
        this.currentImg = this.data.images[Number(this.currentImgIdx)];
    }

    public imgCallback(resp: any): void {
        ++this.loadedImgCount;
        if (resp.status === 200) {
            const img = this.data.images[Number(resp.id)];
            this.loadImages.push(img);
            this.loadImages.sort((a, b) => a.id - b.id);
        }

        if (this.loadedImgCount === this.data.images.length) {
            this.images = this.loadImages.slice(0, 48);
        }
    }

    public reformImageUrl(url: string, size: string): string {
        if (!url || !size) {
            return url;
        }
        return url.replace('{size}', size);
    }

    public plusSlides(n: number): void {
        if (this.currentImgIdx === -1 || !this.images[this.currentImgIdx]) {
            return;
        }

        let selectedIdx = this.currentImgIdx;
        selectedIdx += n;

        if (selectedIdx < 0) {
            selectedIdx = this.images.length - 1;
        }
        if (selectedIdx > this.images.length - 1) {
            selectedIdx = 0;
        }

        this.currentImg = ToolsService.deepCopy(this.images[selectedIdx]) as Gallery;
        this.currentImgIdx = selectedIdx;
    }
}
