import { Injectable } from '@angular/core';
import { AccommodationListItem } from '@schemas/modules/accommodations/accommodation.model';
import { AccommodationListMetadata } from '@schemas/modules/accommodations/accommodation-list-metadata';
import { FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { ServiceId } from '@core/constants/service-id.const';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { DataId } from '@schemas/modules/core/base-types';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { Observable } from 'rxjs';
import { take, tap } from 'rxjs/operators';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { TzServiceTabs } from '../../../../modules/results/tabsUtils/tz-tabs-service';
import { IHotelNames } from '@schemas/modules/accommodations/accommodations-response';

@Injectable({
    providedIn: 'root',
})
export class AccommodationsService {
    private accommodationsList: AccommodationListItem[] = [];
    private nearestAccommodationsList: AccommodationListItem[] = [];
    private hotelNames: IHotelNames[] = [];

    private accommodationsListMetadata: AccommodationListMetadata | undefined;
    private nearestAccommodationsListMetadata: AccommodationListMetadata | undefined;
    private filter: FilterReferenceItem = [];
    private mapHotel: AccommodationListItem | undefined;

    private currentPageIndex = 0;
    private currentItemIndexOnTheList = 0;

    private defaultAccListMetaData = {
        increment: 80,
        section: 1,
        destinationName: '',
        total: 0,
    };
    private defaultNearestAccListMetaData = {
        increment: 80,
        section: 2,
        destinationName: '',
        total: 0,
    };

    private currentDataIdValue: DataId = '';

    constructor(
        private searchDataService: SearchDataService,
        private accommodationDataService: AccommodationsDataService,
        private tzServiceTabs: TzServiceTabs
    ) {}

    get currentPage(): number {
        return this.currentPageIndex;
    }

    get currentItem(): number {
        return this.currentItemIndexOnTheList;
    }

    get currentDataId(): DataId {
        return this.currentDataIdValue;
    }

    public static getAccIndexFromTheList(list: AccommodationListItem[] = [], selectedId: string) {
        return list.map((acc: AccommodationListItem) => acc.giataID).indexOf(selectedId);
    }

    public getAccommodation(id: string): Observable<Accommodation> {
        this.tzServiceTabs.toggleForceLoader(ServiceId.ACC, true);
        const rateCheckObserver = this.accommodationDataService.getAccommodation(id);
        return rateCheckObserver.pipe(
            take(1),
            tap((accRes: Accommodation) => {
                this.tzServiceTabs.toggleForceLoader(ServiceId.ACC, false);
                // after recheck, prices and provider might have changed, update the whole list with
                // new data if any
                const list = accRes.sectionType === 1 ? this.accommodationsList : this.nearestAccommodationsList;
                const index = AccommodationsService.getAccIndexFromTheList(list, accRes.giataID);
                if (index !== -1 && index !== 0) {
                    list[index] = accRes;
                } else if (index === 0) {
                    // list.map((value) => {
                    //     if (value.recommended) {
                    //         list[index] = {
                    //             ...accRes,
                    //             recommended: value.recommended,
                    //         };
                    //     }
                    // });
                }
            })
        );
    }

    public hasAvailableData(): boolean {
        return this.accommodationsList.length !== 0 || this.nearestAccommodationsList.length !== 0;
    }

    public setAccommodationList(
        list: AccommodationListItem[],
        selectedId = '',
        position: number,
        hotelNames: IHotelNames[]
    ): void {
        this.accommodationsList = list;
        this.hotelNames = hotelNames;
        this.currentItemIndexOnTheList = position;
        this.currentDataIdValue = this.searchDataService.getDataId(ServiceId.ACC);
    }

    public setNearestAccommodationList(list: AccommodationListItem[], selectedId = '', position: number): void {
        this.nearestAccommodationsList = list;
        const index = AccommodationsService.getAccIndexFromTheList(list, selectedId);
        if (index !== -1) {
            this.currentItemIndexOnTheList = position;
        }
    }

    public updateAccInfoToList(updatedAcc: Accommodation): void {
        if (!updatedAcc.noRoom) {
            return;
        }

        for (const acc of this.accommodationsList) {
            if (acc.id === updatedAcc.id) {
                acc.noRoom = updatedAcc.noRoom;
            }
        }

        for (const acc of this.nearestAccommodationsList) {
            if (acc.id === updatedAcc.id) {
                acc.noRoom = updatedAcc.noRoom;
            }
        }
    }

    public getAccommodationsList(): AccommodationListItem[] {
        return this.accommodationsList;
    }

    public getNearestAccommodationsList(): AccommodationListItem[] {
        return this.nearestAccommodationsList;
    }

    public getHotelNames(): IHotelNames[] {
        return this.hotelNames;
    }

    public setMetadataOfTheAccommodation(page: number): void {
        this.currentPageIndex = page;
    }

    public getNearestAccommodationListMetadata(): AccommodationListMetadata {
        if (this.nearestAccommodationsListMetadata) {
            return this.nearestAccommodationsListMetadata;
        }
        return this.defaultNearestAccListMetaData;
    }

    public getAccommodationListMetadata(): AccommodationListMetadata {
        if (this.accommodationsListMetadata) {
            return this.accommodationsListMetadata;
        }
        return this.defaultAccListMetaData;
    }

    public setAccommodationListMetadata(
        accommodationsListMetadata: AccommodationListMetadata,
        nearestAccommodationListMetadata: AccommodationListMetadata
    ): void {
        this.accommodationsListMetadata = accommodationsListMetadata;
        this.nearestAccommodationsListMetadata = nearestAccommodationListMetadata;
    }

    public getFilter() {
        return this.filter;
    }

    public setFilter(filter: FilterReferenceItem = []) {
        this.filter = filter;
    }

    public clearMetadataOfTheAccommodation(): void {
        this.currentPageIndex = 0;
        this.currentItemIndexOnTheList = 0;
    }

    public getMapHotel(): AccommodationListItem | undefined {
        return this.mapHotel;
    }

    public setMapHotel(hotel: AccommodationListItem | undefined) {
        this.mapHotel = hotel;
    }
}
