import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { ServicePrices } from '@schemas/modules/core/service-prices';
import { PickerController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-mob-accommodation-details-room-offer',
    templateUrl: './mob-accommodation-details-room-offer.component.html',
    styleUrls: ['./mob-accommodation-details-room-offer.component.scss'],
})
export class MobAccommodationDetailsRoomOfferComponent implements OnInit, OnChanges {
    @Input() public roomOffer!: RoomOption;
    @Input() public isUserLogged!: boolean;
    @Input() public isSelected = false;
    @Input() public maxRoomSelectionNumber = 0;
    @Input() public stayNumber!: number;
    @Input() public actionsButtons: string[] = [];
    @Output() public roomCountChanged: EventEmitter<void> = new EventEmitter<void>();
    @Output() public getRoomPrice: EventEmitter<{ price: number; id: string }> = new EventEmitter<{
        price: number;
        id: string;
    }>();
    @Output() public openTermsModal: EventEmitter<void> = new EventEmitter<void>();
    @Output() public removeOption: EventEmitter<string> = new EventEmitter<string>();
    public roomPrice!: ServicePrices;
    public visitorsEmptyArr: unknown[] = [];
    public numberOfNights!: number;
    public maxValue = 0;

    constructor(
        private pickerCtrl: PickerController,
        private translateService: TranslateService,
        private cartService: CartService
    ) {}

    get checkShowTerms(): boolean {
        return AccommodationsUtilsService.checkShowTerms(this.roomOffer, this.isUserLogged);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (
            changes['maxRoomSelectionNumber'] &&
            (changes['maxRoomSelectionNumber'] || changes['maxRoomSelectionNumber'] === 0)
        ) {
            const personCount = this.roomOffer.personCount;
            const roomsLeft = this.roomOffer.roomsLeft;
            this.maxValue = AccommodationsUtilsService.getMaxRoomSelectionNumber(
                { personCount, roomsLeft },
                this.maxRoomSelectionNumber
            );
        }
    }

    public ngOnInit(): void {
        this.initData();
        this.roomPrice = this.roomOffer?.prices;
        this.maxValue = Math.min(this.maxRoomSelectionNumber, this.roomOffer?.roomsLeft);
    }

    public roomCapacity(capacity: number): number[] {
        return new Array(capacity);
    }

    public onSelectRoomOffer(): void {
        this.maxValue = Math.min(this.maxRoomSelectionNumber, this.roomOffer?.roomsLeft);

        if (this.roomOffer.personCount > 0) {
            this.openRoomPicker(this.maxValue);
            return;
        }

        if (this.maxValue > 0) {
            if (this.roomOffer.personCount === 0) {
                this.roomOffer.personCount = 1;
                this.roomCountChanged.emit();
                this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOffer?.id });
            }
        }
    }

    public onOpenTermsModal(): void {
        this.openTermsModal.emit();
    }

    private async openRoomPicker(maxRoomSelectionNumber: number): Promise<void> {
        if (this.roomOffer?.roomsLeft > this.maxValue) {
            maxRoomSelectionNumber = maxRoomSelectionNumber + this.roomOffer?.personCount;
        }

        const options: { text: string; value: string }[] = [];

        for (let i = maxRoomSelectionNumber; i > 0; i--) {
            options.push({ text: i + '', value: i + '' });
        }

        if (!options.length) {
            options.push({ text: '1', value: '1' });
        }

        const picker = await this.pickerCtrl.create({
            columns: [
                {
                    name: 'rooms',
                    options,
                },
            ],
            buttons: [
                {
                    text: this.translateService.instant('GENERAL.SELECT'),
                    handler: (value) => {
                        this.roomOffer.personCount = parseInt(value.rooms.value, 10);
                        this.roomCountChanged.emit();
                        this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOffer?.id });
                    },
                },
                {
                    text: this.translateService.instant('GENERAL.remove'),
                    handler: (value) => {
                        this.roomOffer.personCount = 0;
                        this.roomCountChanged.emit();
                        this.getRoomPrice.emit({ price: this.roomPrice.listPrice, id: this.roomOffer?.id });
                    },
                    cssClass: 'mob-room-remove-btn',
                },
            ],
            cssClass: 'mob-room-picker',
        });

        await picker.present();
    }

    private initData(): void {
        this.visitorsEmptyArr = this.roomCapacity(this.roomOffer?.capacity);
        this.numberOfNights = this.cartService.getCartData(ServiceId.ACC).items[0].selectedItem.data.numberOfNights;
    }
}
