import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SupplierClass, TrainClass } from '@schemas/modules/trains/train-class';
import { TrainService } from '@services/modules/transport/trains/trains.service';
import { TRAIN } from '@schemas/modules/trains/train';
import {
    SelectedOption,
    SupplierClassMinPrice,
    TrainSelection,
} from '@schemas/modules/trains/train-search-query-params';
import { DialogService } from '@services/dialog.service';
import { ModalService } from '@services/modal.service';
import { MobTrainTermsModalComponent } from '../mob-train-terms-modal/mob-train-terms-modal.component';
import { TranslateService } from '@ngx-translate/core';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-mob-train-class-details',
    templateUrl: './mob-train-class-details.component.html',
    styleUrls: ['./mob-train-class-details.component.scss'],
})
export class MobTrainClassDetailsComponent implements OnInit {
    @Input() train!: TRAIN;
    @Input() supplierClass!: SupplierClass;
    @Input() supplierClassMinPrice: SupplierClassMinPrice = {};
    @Input() isSelected = false;
    @Input() selectedSubClass: string | undefined;
    @Input() selectedOption: SelectedOption | undefined;
    @Output() selectTicket: EventEmitter<TrainClass> = new EventEmitter<TrainClass>();
    @Output() selectedClassId: EventEmitter<string> = new EventEmitter<string>();

    public trainClasses: TrainClass[] = [];
    public unwrappedClass: string | undefined;
    public isSubClasses!: boolean;
    selectedTrain: TrainSelection | undefined;

    constructor(
        private trainService: TrainService,
        private dialogService: DialogService,
        private modalService: ModalService,
        private translateService: TranslateService,
        private scrollService: ScrollService
    ) {}

    get isValidTicket(): boolean {
        return this.trainService.isValidTicket(this.train, this.supplierClass);
    }

    ngOnInit() {
        this.loadTrainSubClasses(this.supplierClass.class);

        if (this.selectedOption && this.train.journeyType === 'returnJourney' && this.selectedOption.return) {
            this.selectedTrain = this.selectedOption.return;
        }
        if (this.selectedOption && this.train.journeyType === 'onwardJourney' && this.selectedOption.onward) {
            this.selectedTrain = this.selectedOption.onward;
        }

        this.isSubClasses = this.checkTrainHasSubClasses();
    }

    public openTrainTerms() {
        this.modalService.createMainModal(MobTrainTermsModalComponent, {
            data: {
                operator: this.trainClasses[0].operator,
                terms: this.trainClasses[0].trainClass.ClassDescription.termsandConditionsList,
            },
            title: this.translateService.instant('FLIGHT_TRAIN.CONDITIONS'),
            cssClass: 'mob-terms-modal',
        });
    }

    public onSelectTicket(supplierClass: TrainClass, element: string): void {
        setTimeout(() => {
            requestAnimationFrame(() => {
                this.scrollService.scrollToVisibleArea(element, 'trainDetailsFooter', {
                    scrollMarginBottom: 30,
                    scrollBlock: 'end',
                });
            });
        }, 300);

        if (this.selectedSubClass === supplierClass.id) {
            this.selectedClassId.emit('');
            return;
        }

        if (!this.isValidTicket) {
            return;
        }

        this.selectTicket.emit(supplierClass);
        this.selectedClassId.emit(supplierClass.id);
    }

    private loadTrainSubClasses(trainClass: string): void {
        this.trainClasses = this.train.trainClasses.filter((tc) => tc.trainClass.tzClass === trainClass);
    }

    private checkTrainHasSubClasses(): boolean {
        return this.train.supplierClasses.some((supplierClass: SupplierClass) => supplierClass.count > 1);
    }
}
