import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { TrainClass } from '@schemas/modules/trains/train-class';

@Component({
    selector: 'tz-mob-trains-list-item-info-block',
    templateUrl: './mob-trains-list-item-info-block.component.html',
    styleUrls: ['./mob-trains-list-item-info-block.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobTrainsListItemInfoBlockComponent implements OnInit {
    @Input() train: TRAIN | undefined;
    @Input() totalPassengers = 0;
    @Input() trainClass: TrainClass | undefined;
    @Input() trainType: 'depart' | 'return' = 'depart';
    public directionFrom: 'departure' | 'arrival' = 'departure';
    public directionTo: 'departure' | 'arrival' = 'arrival';
    public isReturnWay = false;
    public stops: number[] = [];
    public data: TRAIN | TrainClass | undefined;

    get arrivalSameDay(): boolean {
        return this.data?.arrival.dateTzFormat.split('T')[0] === this.data?.departure.dateTzFormat.split('T')[0];
    }

    public ngOnInit(): void {
        this.prepareData();
    }

    private fixAssetsPath(): void {
        const oldPath = '/web/assets/img/trainSupplierLogos/';
        const newPath = '/assets/icons/web/img/trainSupplierLogos/';
        if (this.data) {
            this.data.operatorImage = this.data.operatorImage.replace(oldPath, newPath);
        }
    }

    private prepareData(): void {
        this.isReturnWay = this.trainType === 'return';

        if (this.train) {
            this.data = this.train;
        }

        if (this.trainClass) {
            this.data = this.trainClass;
        }

        if (this.trainType !== 'depart') {
            this.directionFrom = 'arrival';
            this.directionTo = 'departure';
        }

        this.stops = typeof this.data?.stops === 'number' ? Array(this.data?.stops).fill(1) : [];

        this.fixAssetsPath();
    }
}
