import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { Room } from '@schemas/modules/accommodations/room';
import { AccommodationRatings } from '@schemas/modules/accommodations/accommodation-ratings.model';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { ToolsService } from '@core/utils/tools-service';

@Component({
    selector: 'tz-accommodation-room',
    templateUrl: './accommodation-room.component.html',
    styleUrls: ['./accommodation-room.component.scss'],
})
export class AccommodationRoomComponent implements OnInit {
    @Input() room!: Room;
    @Input() ratings!: AccommodationRatings;
    @Input() numberOfNights!: number;
    @Input() fromSelected = false;
    @Input() hideOptions = false;
    @Input() isUserLogged!: boolean;
    @Input() roomBookings: ICartSelectedItemBookingOption[] = [];
    @Input() roomsLeftForSelection = 0;
    @Output() roomCountChanged: EventEmitter<number> = new EventEmitter<number>();
    @Output() roomMaxSelectedCountReached: EventEmitter<void> = new EventEmitter<void>();
    @Output() openTermsModal: EventEmitter<RoomOption> = new EventEmitter<RoomOption>();

    public showRoomsOptionsNumbers = 3;
    public selectedRoomOptions: { [key: string]: number } = {};
    public cartRoomOptions: { [key: string]: number } = {};
    public bookedRoomOptions: { [key: string]: number } = {};

    constructor() {}

    ngOnInit() {
        this.showRoomsOptionsNumbers = this.room.roomOffers.length > 3 ? 2 : this.room.roomOffers.length;
        this.getCartRoomOptions();
    }

    public onOptionCountChanged($event: number, id: string) {
        this.selectedRoomOptions[id] = $event;
        let count = 0;
        for (const option in this.selectedRoomOptions) {
            if (this.selectedRoomOptions[option] !== 0) {
                count++;
            }
        }
        this.roomCountChanged.emit(count);
    }

    public onViewOptionsToggle(): void {
        this.showRoomsOptionsNumbers =
            this.showRoomsOptionsNumbers < this.room.roomOffers.length ? this.room.roomOffers.length : 2;
    }

    public onRoomMaxSelectedCountReached(): void {
        this.roomMaxSelectedCountReached.emit();
    }

    private getCartRoomOptions(): void {
        let validatedOptionIdx = 0;
        for (const option of this.roomBookings) {
            const roomOfferId = option.selectedRoom?.roomOfferId;
            if (roomOfferId) {
                const cartOfferCount = this.cartRoomOptions[roomOfferId];
                this.cartRoomOptions[roomOfferId] = cartOfferCount ? cartOfferCount + 1 : 1;

                if (CartUtils.isBooked(option.status)) {
                    const bookedCount = this.bookedRoomOptions[roomOfferId];
                    this.bookedRoomOptions[roomOfferId] = bookedCount ? bookedCount + 1 : 1;
                }

                const offerIdx = this.room.roomOffers.findIndex((offer) => offer.id === roomOfferId) + 1;
                if (offerIdx > validatedOptionIdx) {
                    validatedOptionIdx = offerIdx;
                }
            }
        }
        this.selectedRoomOptions = ToolsService.deepCopy(this.cartRoomOptions);
        if (validatedOptionIdx > this.showRoomsOptionsNumbers) {
            this.showRoomsOptionsNumbers = validatedOptionIdx;
        }
    }
}
