import { Component, EventEmitter, Input, OnInit, AfterContentChecked, Output, ChangeDetectorRef } from '@angular/core';

import { Flight } from '@schemas/modules/flights/flight';

@Component({
    selector: 'tz-flight-item-desc-packages',
    templateUrl: './flight-item-desc-packages.component.html',
    styleUrls: ['./flight-item-desc-packages.component.scss'],
})
export class FlightItemDescPackagesComponent implements OnInit, AfterContentChecked {
    @Input() packages: Flight[] | undefined = [];
    @Input() selectedPackageIndex: number | undefined | null = 0;
    @Input() packageStartIdx = 0;

    @Output() changeSelectedPackageIndex = new EventEmitter<{ pkgIndex: number; packageStartIdx: number }>();

    public fromIdx = 0;
    public luggageBlockCount = 0;

    constructor(private changeDetector: ChangeDetectorRef) {}

    ngAfterContentChecked(): void {
        this.changeDetector.detectChanges();
    }

    ngOnInit(): void {
        if (this.packageStartIdx > 0) {
            this.fromIdx = this.packageStartIdx;
        }
    }

    public navigatePackage(value: number): void {
        this.fromIdx += value;
    }

    public selectPackage(flightId: string): void {
        const packageIdx = this.packages?.findIndex((flight) => flight.id === flightId);
        if (packageIdx !== undefined) {
            this.changeSelectedPackageIndex.emit({ pkgIndex: packageIdx, packageStartIdx: this.fromIdx });
        }
    }

    public luggageBlockCountChange(luggageBlockCount: number): void {
        if (luggageBlockCount > this.luggageBlockCount) {
            this.luggageBlockCount = luggageBlockCount;
        }
    }
}
