import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { ScrollService } from '@services/modules/core/scroll-service';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-car-rental-packages',
    templateUrl: './car-rental-packages.component.html',
    styleUrls: ['./car-rental-packages.component.scss', './car-rental-packages.component.mobile.scss'],
})
export class CarRentalPackagesComponent implements OnInit {
    @Input() packages: CarRentalPackage[] = [];
    @Input() currentKey = '';
    @Input() days: number | null = null;
    @Input() mobIsTypeDetails = false;
    @Output() selectPackage: EventEmitter<CarRentalPackage> = new EventEmitter<CarRentalPackage>();
    @Output() packageSelected: EventEmitter<boolean> = new EventEmitter<boolean>();

    public fromIdx = 0;
    public selectedRentalPackage!: CarRentalPackage | null;
    public isPackageSelected = true;

    constructor(private scrollService: ScrollService, public platformService: PlatformService) {}

    ngOnInit() {
        if (!this.mobIsTypeDetails) {
            this.selectedRentalPackage = this.packages[0];
            // } else if (this.mobIsTypeDetails && this.packages.length === 1) {
            //     this.selectedRentalPackage = this.packages[0];
            //     this.packageSelected.emit(true);
        } else {
            this.isPackageSelected = false;
        }
    }

    public selectRentalPackage(rentalPackage: CarRentalPackage, element: string): void {
        if (this.platformService.isMobileDevice) {
            requestAnimationFrame(() => {
                this.scrollService.scrollToVisibleArea(element, 'carDetailsFooter', {
                    scrollTop: 18,
                    includeElementsHeight: ['mobMainModalHeader'],
                });
            });
        }

        if (this.selectedRentalPackage && this.selectedRentalPackage.packageKey === rentalPackage.packageKey) {
            this.packageSelected.emit(false);
            this.isPackageSelected = false;
            this.selectedRentalPackage = null;
            return;
        }

        this.isPackageSelected = true;
        this.selectedRentalPackage = rentalPackage;
        this.packageSelected.emit(true);
        this.selectPackage.emit(rentalPackage);
    }

    public navigatePackage(value: number): void {
        this.fromIdx += value;
    }

    public getPackageLimit(): number {
        return window.innerWidth < 768 ? this.packages.length : this.fromIdx + 2;
    }
}
