import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { ICart } from '@schemas/modules/cart/cart';
import { CombinationId } from '@schemas/modules/core/base-types';
import { PaymentCaptureMode } from '@core/constants/cart-status';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';

@Component({
    selector: 'tz-selected-transfers',
    templateUrl: './selected-transfers.component.html',
    styleUrls: ['./selected-transfers.component.scss'],
})
export class SelectedTransfersComponent implements OnInit {
    @Input() serviceList = false;
    @Input() combId!: CombinationId;
    @Input() transferCartData!: ICart;
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Input() isSummary = false;
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeTransfer: EventEmitter<Transfer> = new EventEmitter<Transfer>();
    @Output() selectedId: EventEmitter<string> = new EventEmitter<string>();
    @Output() cancel: EventEmitter<string> = new EventEmitter<string>();
    public selectedTransfer!: Transfer;
    public totalPassengers = 0;

    constructor() {}

    ngOnInit() {
        const travellersData = CartUtils.getTravellersData(this.transferCartData.items);
        if (travellersData) {
            this.totalPassengers = travellersData.total;
        }
    }

    public onRemove(optionId: string): void {
        this.removedItem.emit(optionId);
    }

    public onChangeTransfer(data: Transfer): void {
        this.changeTransfer.emit(data);
    }

    public onCancel(itineraryId: string): void {
        this.cancel.emit(itineraryId);
    }

    public setSelectedId(itemId: string): void {
        this.selectedId.emit(itemId);
    }

    public onChangeOptions(): void {}

    public sortTransfer(transfers: ICartItem[]): ICartItem[] {
        return transfers.sort((a, b) =>
            a.selectedItem.data.journeyType > b.selectedItem.data.journeyType
                ? 1
                : a.selectedItem.data.journeyType === b.selectedItem.data.journeyType
                ? 0
                : -1
        );
    }
}
