import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { FlightSummary, FlightSummaryType } from '@schemas/modules/flights/flight-summary.model';

@Component({
    selector: 'tz-mob-flight-details-route-details',
    templateUrl: './mob-flight-details-route-details.component.html',
    styleUrls: ['./mob-flight-details-route-details.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobFlightDetailsRouteDetailsComponent implements OnInit {
    @Input() flight!: Flight;
    @Input() itemType: FlightSummaryType = 'depart';
    public departSummary!: FlightSummary;
    public returnSummary!: FlightSummary;
    public departStops: undefined | number;
    public returnStops: undefined | number;
    constructor() {}

    get isDepartItem(): boolean {
        return this.itemType === 'depart';
    }

    get isReturnItem(): boolean {
        return this.itemType === 'return';
    }

    ngOnInit() {
        this.setFlightSummeryData();
        this.initializeNumberOfStops();
        console.log('departSummaryFlight', this.departSummary);
        console.log('returnSummaryFlight', this.returnSummary);
    }

    setFlightSummeryData(): void {
        this.departSummary = this.flight.departureSummary;
        if (this.flight.isRoundTrip) {
            this.returnSummary = this.flight.returnSummary;
        }
    }
    private initializeNumberOfStops(): void {
        if (this.flight.departureSummary.airportStopRoutes) {
            this.departStops = this.departSummary?.airportStopRoutes?.length;
        }
        if (this.flight.returnSummary.airportStopRoutes) {
            this.returnStops = this.returnSummary?.airportStopRoutes?.length;
        }
    }
}
