import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CombinationId } from '@schemas/modules/core/base-types';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { TRAIN } from '@schemas/modules/trains/train';
import { TzSelectedService } from '../../../../selectedUtils/tz-selected-service';
import { ModalService } from '@services/modal.service';
import { MobSelectedTrainDetailsComponent } from '../mob-selected-train-details/mob-selected-train-details.component';
import { PlatformService } from '@services/platform.service';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';

@Component({
    selector: 'tz-selected-trains',
    templateUrl: './selected-trains.component.html',
    styleUrls: ['./selected-trains.component.scss'],
})
export class SelectedTrainsComponent implements OnInit {
    @Input() serviceList = false;
    @Input() combId!: CombinationId;
    @Input() trainItems!: ICartItem[];
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Input() showActionBar!: boolean;
    @Input() showTitle!: boolean;
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeTickets: EventEmitter<void> = new EventEmitter();
    @Output() changeOptions: EventEmitter<TRAIN> = new EventEmitter();
    @Output() selectedId: EventEmitter<string> = new EventEmitter<string>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
    public buttons: string[] = [];
    public optionId!: string;
    public invalidCartItem = false;

    constructor(private platformService: PlatformService, private modalService: ModalService) {}

    ngOnInit() {
        this.initializeActionBarButtons();

        this.invalidCartItem = !this.trainItems[0].selectedItem.bookingOptions.some(
            (option: ICartSelectedItemBookingOption) =>
                !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
        );
    }

    public onRemoveSelected(): void {
        this.removedItem.emit(this.optionId);
    }

    public onChangeTickets(): void {
        this.changeTickets.emit();
    }

    public onChangeOptions(): void {
        this.changeOptions.emit();
    }

    public onCancelTicket(): void {
        this.cancel.emit();
    }

    public onRemoveItem(): void {}

    public setSelectedId(itemId: string): void {
        this.selectedId.emit(itemId);
    }

    public onMobShowSelectedDetails(): void {
        if (!this.platformService.isMobileDevice) {
            return;
        }

        this.modalService.createDetailsPage(MobSelectedTrainDetailsComponent, {
            data: {
                trains: this.trainItems,
                isRoundTrip: this.trainItems.length > 1,
            },
            title: 'TRAIN.SELECTED_TRAINS',
        });
    }

    private initializeActionBarButtons() {
        this.buttons = TzSelectedService.initializeActionBarButtons(
            this.trainItems[0].selectedItem,
            this.fromBookings,
            this.serviceList,
            this.paymentCaptureMode,
            false
        );

        if (!this.fromBookings && !CartUtils.hasBooked(this.trainItems[0].selectedItem.bookingOptions)) {
            const travellersData = CartUtils.getTravellersData([this.trainItems[0]]);
            this.buttons.push(travellersData && travellersData.total > 1 ? 'changeTickets' : 'changeTicket');

            if (!CartUtils.hasBookingStarted(this.trainItems[0].selectedItem.bookingOptions)) {
                this.buttons.push('changeOptions');
            } else if (CartUtils.hasUnavailable(this.trainItems[0].selectedItem.bookingOptions)) {
                this.buttons.push('update');
            }
        }
    }
}
