import { Component, Input, OnInit } from '@angular/core';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceId } from '@core/constants/service-id.const';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { Room } from '@schemas/modules/accommodations/room';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CartRemoveService } from '@services/modules/cart/services/cart-remove-service';
import { ICartSelectedItem } from '@schemas/modules/cart/cart-selected-item';

@Component({
    selector: 'tz-mob-accommodation-selected-room-list',
    templateUrl: './mob-accommodation-selected-room-list.component.html',
    styleUrls: ['./mob-accommodation-selected-room-list.component.scss'],
})
export class MobAccommodationSelectedRoomListComponent implements OnInit {
    @Input() public accommodation!: Accommodation;
    @Input() public selectedItem!: ICartSelectedItem;
    @Input() public actionsButtons: string[] = [];
    @Input() public selectedRoomsData!: { rooms: Room[]; options: { [key: number]: ICartSelectedItemBookingOption[] } };
    public selectedOptionsList!: ICartSelectedItemBookingOption[];
    private servTypes = ServiceId;

    constructor(private cartService: CartService, private cartRemoveService: CartRemoveService) {}

    public ngOnInit(): void {
        this.initData();
    }

    public removeRoomOption(id: string): void {
        this.cartRemoveService.removeTheService(this.servTypes.ACC, id, true).then(
            (isRemoved: boolean) => {
                if (isRemoved) {
                    this.selectedOptionsList = this.cartService
                        .getCartData(ServiceId.ACC)
                        .items.find((service: ICartItem) => service.dataType === 'hotel')?.selectedItem.bookingOptions;
                }
            },
            (error) => {
                console.error(error);
            }
        );
    }

    private initData(): void {
        this.selectedOptionsList = this.selectedItem.bookingOptions;
    }
}
