import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CarRental } from '@schemas/modules/car-rentals/car-rental';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CombinationId } from '@schemas/modules/core/base-types';
import { TzServiceTabs } from '../../../tabsUtils/tz-tabs-service';

@Component({
    selector: 'tz-mob-car-rental-list-item',
    templateUrl: './mob-car-rental-list-item.component.html',
    styleUrls: ['./mob-car-rental-list-item.component.scss'],
})
export class MobCarRentalListItemComponent implements OnInit {
    @Input() carRental: CarRental | null | undefined;
    @Input() carRentalItem: ICartItem | undefined;
    @Input() oldPrice = 0;
    @Input() isPopup = false;
    @Input() serviceList = false;
    @Input() combId: CombinationId | undefined;

    @Output() selectedCarRental: EventEmitter<void> = new EventEmitter<void>();

    carName: string | undefined = '';
    carSimilar = false;
    public changedPriceDiff = '';

    constructor(private serviceTabs: TzServiceTabs) {}

    public ngOnInit(): void {
        this.initData();
        if (this.carRental) {
            this.carName = this.carRental?.vehicle.name.toLowerCase() || '';

            /*const indexSimilar = this.carName.indexOf('or similar');

            if (indexSimilar > 0) {
                this.carName = this.carName.slice(0, indexSimilar);
                this.carSimilar = true;
            }*/
        }
    }

    private initData(): void {
        const currentCartPrice = this.carRental?.currentPackage.totalPrice.listPrice;

        if (this.oldPrice > 0 && currentCartPrice) {
            this.changedPriceDiff = this.serviceTabs.updatePriceChanging(this.oldPrice, currentCartPrice);
        }
    }
}
