import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Room } from '@schemas/modules/accommodations/room';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceId } from '@core/constants/service-id.const';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';

@Component({
    selector: 'tz-mob-accommodation-selected-room-details',
    templateUrl: './mob-accommodation-selected-room-details.component.html',
    styleUrls: ['./mob-accommodation-selected-room-details.component.scss'],
})
export class MobAccommodationSelectedRoomDetailsComponent implements OnInit {
    @Input() public selectedRoom!: ICartSelectedItemBookingOption;
    @Input() public accommodation!: Accommodation;
    @Input() public actionsButtons: string[] = [];
    @Input() public selectedRoomQuantity = 0;
    @Output() public removeRoomOption: EventEmitter<string> = new EventEmitter();
    public numberOfNights!: number;
    public room!: Room | undefined;

    constructor(private cartService: CartService) {}

    public ngOnInit(): void {
        this.initData();
    }

    public removeOption(id: string): void {
        this.removeRoomOption.emit(id);
    }

    private initData(): void {
        this.room = this.accommodation.rooms.find((room: Room) => room.id === this.selectedRoom.selectedRoom?.roomId);
        this.numberOfNights = this.cartService.getCartData(ServiceId.ACC).items[0].selectedItem.data.numberOfNights;
    }
}
