import { Component, OnInit } from '@angular/core';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'tz-mob-selected-train-details',
    templateUrl: './mob-selected-train-details.component.html',
    styleUrls: ['./mob-selected-train-details.component.scss'],
})
export class MobSelectedTrainDetailsComponent implements OnInit {
    public isRoundTrip!: boolean;
    public trains!: ICartItem[];
    public transportRoute = '';

    private data!: {
        trains: ICartItem[];
        isRoundTrip: boolean;
    };

    constructor(private translateService: TranslateService) {}

    public ngOnInit(): void {
        this.setTrainsData();
        this.getTrainRoute();
    }

    private getTrainRoute(): void {
        const { from, to } = this.trains[0]?.searchReq.searchData;

        this.transportRoute = this.translateService.instant('TRAIN.SELECTED', {
            transportRoute: `${from.cityName}  ${to.cityName}`,
        });
    }

    private setTrainsData(): void {
        this.isRoundTrip = this.data.isRoundTrip;
        this.trains = this.data.trains;
    }
}
