import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { FilterItemCheckbox } from '@schemas/modules/core/filters/service-filter';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-filter-checkbox',
    templateUrl: './checkbox.component.html',
    styleUrls: ['./checkbox.component.scss'],
})
export class FilterCheckboxComponent implements OnInit {
    @Input() selected? = false;
    @Input() label?: string;
    @Input() checkbox!: FilterItemCheckbox;
    @Input() className = '';
    @Input() stopPropagation = false;
    @Input() isMobileStops = false;
    @Input() isMobileStar = false;
    @Input() hideCheckBoxIcon = false;
    @Input() hideCheckBoxCount = false;
    @Input() stops = false;
    @Output() selectedChange: EventEmitter<boolean> = new EventEmitter<boolean>();

    constructor(public platformService: PlatformService) {}

    ngOnInit(): void {}

    public onChange($event: Event): void {
        this.selectedChange.emit(($event.target as HTMLInputElement).checked);
    }

    public onClick($event: Event): void {
        if (this.stopPropagation) {
            $event.stopPropagation();
        }
    }
}
