import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';

import {
    FlightLuggage,
    FlightLuggageOption,
    FlightLuggageOptionTypes,
} from '@schemas/modules/flights/flight-luggage.model';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-flight-list-item-desc-luggage',
    templateUrl: './flight-list-item-desc-luggage.component.html',
    styleUrls: ['./flight-list-item-desc-luggage.component.scss'],
})
export class FlightListItemDescLuggageComponent implements OnChanges, OnInit {
    @Input() luggage!: FlightLuggage;
    @Input() componentType: 'details' | 'package' = 'details';
    @Output() luggageBlockCountChange: EventEmitter<number> = new EventEmitter<number>();

    public bags: { option: FlightLuggageOption; hide: boolean; icon: string; text: string; style: string }[] = [];

    constructor(public platformService: PlatformService) {}

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['luggage'] && !changes['luggage'].firstChange) {
            this.orderLuggage();
        }
    }

    ngOnInit() {
        this.orderLuggage();
    }

    orderLuggage(): void {
        const luggage = { ...this.luggage };
        const orderingBags: {
            option: FlightLuggageOption;
            hide: boolean;
            icon: string;
            text: string;
            style: string;
        }[] = [];
        if (luggage.cabin && luggage.cabin.options) {
            luggage.cabin.options.map((option) => {
                if (
                    !(
                        (option.prices && option.prices.listPrice > 0) ||
                        (option.travelerTypes?.length === 1 && option.travelerTypes.includes('Child')) ||
                        (option.travelerTypes?.length === 1 && option.travelerTypes.includes('Infant'))
                    )
                ) {
                    orderingBags.push({ option, hide: false, icon: '', text: '', style: '' });
                }
            });
        }
        if (luggage.checkIn && luggage.checkIn.options) {
            luggage.checkIn.options.map((option) => {
                if (
                    !(
                        (option.prices && option.prices.listPrice > 0) ||
                        (option.travelerTypes?.length === 1 && option.travelerTypes.includes('Child')) ||
                        (option.travelerTypes?.length === 1 && option.travelerTypes.includes('Infant'))
                    )
                ) {
                    orderingBags.push({ option, hide: false, icon: '', text: '', style: '' });
                }
            });
        }

        let luggageBlockCount = orderingBags.length;

        orderingBags.map((optionItem) => {
            if (optionItem.option.type === FlightLuggageOptionTypes.PERSONALITEM) {
                optionItem.icon = 'case-small-black';
                optionItem.style = 'width: 1rem; height: 1rem;';
            }

            if (optionItem.option.type === FlightLuggageOptionTypes.CABIN) {
                optionItem.icon = 'case-small-black';
                optionItem.style = 'width: 1rem; height: 1rem;';
            }

            if (optionItem.option.type === FlightLuggageOptionTypes.CHECKIN) {
                optionItem.icon = 'case-dark';
                optionItem.style = 'width: 1rem; height: 1.375rem;';
            }

            optionItem.text = optionItem.option.type;

            if (orderingBags.length > 2 && optionItem.option.type === FlightLuggageOptionTypes.PERSONALITEM) {
                --luggageBlockCount;
                optionItem.hide = true;
            }
        });

        if (luggageBlockCount) {
            this.changeBlockCount(luggageBlockCount);
        }

        this.bags = [...orderingBags];
    }

    private changeBlockCount(luggageBlockCount: number): void {
        this.luggageBlockCountChange.emit(luggageBlockCount);
    }
}
