import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'tz-filter-btn',
    templateUrl: './filter-btn.component.html',
    styleUrls: ['./filter-btn.component.scss'],
})
export class FilterBtnComponent {
    @Input() filterCount = 0;
    @Output() clickFilter: EventEmitter<void> = new EventEmitter<void>();
    @Output() clearFilter: EventEmitter<void> = new EventEmitter<void>();

    public onClickFilter(): void {
        this.clickFilter.emit();
    }

    public onClearFilter(): void {
        this.clearFilter.emit();
    }
}
