import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { ICartSelectedItem } from '@schemas/modules/cart/cart-selected-item';
import { AccommodationListItem } from '@schemas/modules/accommodations/accommodation.model';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { DialogService } from '@services/dialog.service';
// eslint-disable-next-line max-len
import { AccommodationTermsPopupComponent } from '../../../accommodation/components/accommodation-terms-popup/accommodation-terms-popup.component';
import { UserService } from '@services/modules/user/services/user.service';
import { TzSelectedService } from '../../../selectedUtils/tz-selected-service';
import { PlatformService } from '@services/platform.service';
import { Room } from '@schemas/modules/accommodations/room';
import { ToolsService } from '@core/utils/tools-service';
import { ModalService } from '@services/modal.service';
// eslint-disable-next-line max-len
import { MobSelectedAccommodationDetailsComponent } from '../mob-selected-accommodation-details/mob-selected-accommodation-details.component';
// eslint-disable-next-line max-len
import { MobAccommodationRoomsComponent } from '../../../accommodation/components/mob-accommodation-rooms/mob-accommodation-rooms.component';
import { ISelectedHotel } from '../../accommodations.component';
import { BookingSummary } from '@schemas/modules/bookings/booking-summary';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';

@Component({
    selector: 'tz-selected-accommodations',
    templateUrl: './selected-accommodations.component.html',
    styleUrls: ['./selected-accommodations.component.scss'],
})
export class SelectedAccommodationsComponent implements OnInit, OnChanges {
    @Input() accommodationCartData!: IAccommodationsCartData;
    @Input() combId!: string;
    @Input() serviceList = false;
    @Input() detailPage = false;
    @Input() fromBookings = false;
    @Input() bookingSummary!: BookingSummary;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Output() removedItem: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeHotel: EventEmitter<void> = new EventEmitter<void>();
    @Output() changeRooms: EventEmitter<ISelectedHotel> = new EventEmitter<ISelectedHotel>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<string[]> = new EventEmitter<string[]>();
    @Output() gotoHotel = new EventEmitter<void>();

    public readonly maxRoomQuantity = 4;
    public selectedAccommodation!: AccommodationListItem;
    public selectedCartItem!: ICartSelectedItem;
    public selectedAccommodationOptions: ICartSelectedItemBookingOption[] = [];
    public buttons: string[] = [];
    public isUserLogged!: boolean;
    public roomButtons: string[][] = [[]];
    public invalidCartItem = false;
    public selectedRooms: { rooms: Room[]; options: { [key: number]: ICartSelectedItemBookingOption[] } } = {
        rooms: [],
        options: {},
    };
    public roomsLeftForSelection = this.maxRoomQuantity;

    constructor(
        private dialogService: DialogService,
        public userService: UserService,
        public platformService: PlatformService,
        private modalService: ModalService
    ) {}

    get itemPrice(): number {
        const selectedBookingOptions = this.accommodationCartData.items[0].selectedItem.bookingOptions;
        if (selectedBookingOptions.length === 0) {
            return this.selectedAccommodation.cheapestPrice.listPrice;
        }

        let price = 0;
        selectedBookingOptions.map((item) => {
            price += item.itemPrice.listPrice;
        });

        if (!price) {
            selectedBookingOptions.map((item) => {
                price += item.selectedRoom.selectedOffer.prices.listPrice;
            });
        }

        return price;
    }

    get totalPersons(): number {
        let personsCount = 0;
        const selectedBookingOptions = this.accommodationCartData.items[0].selectedItem.bookingOptions;
        selectedBookingOptions.map((item) => {
            if (item.selectedRoom) {
                personsCount += item.selectedRoom.selectedOffer.capacity;
            }
        });
        return personsCount;
    }

    public ngOnInit(): void {
        this.formSelectedAccommodation();
        this.initializeActionBarButtons();
        this.isUserLogged = this.userService.IsLogged();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes['accommodationCartData']) {
            this.formSelectedAccommodation();
            this.initializeActionBarButtons();
        }
    }

    public onMobShowSelectedDetails(event?: Event): void {
        event?.stopPropagation();
        if (!this.platformService.isMobileDevice) {
            return;
        }

        this.modalService.createDetailsPage(MobSelectedAccommodationDetailsComponent, {
            data: {
                accommodationCartData: this.selectedAccommodation,
                selectedRooms: this.selectedRooms,
                bookingSummary: this.bookingSummary,
            },
            title: 'HOTELS.SELECTED',
            isHeaderBottomDivider: false,
        });
    }

    public onRemoveSelected(optionId?: string): void {
        this.removedItem.emit(optionId);
        /*this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.DELETE_PERMANENTLY'),
                content: this.translateService.instant('POPUPS.REMOVE_CART_ITEM.MSG', {
                    serviceName: this.translateService.instant('SERVICES.0').toLowerCase(),
                }),
                confirmBtnTitle: this.translateService.instant('POPUPS.YES_DEL'),
                cancelBtnTitle: this.translateService.instant('POPUPS.CANCEL'),
            })
            .afterClosed()
            .subscribe((dialogReturn: TzDialogReturn) => {
                if (dialogReturn === CONFIRM) {
                    this.removedItem.emit(optionId);
                }
            });*/
    }

    public onDebugDialogOpened(): void {}

    public onChangeOptions(): void {}

    public onChangeRooms(): void {
        if (!this.platformService.isMobileDevice) {
            this.changeRooms.emit({
                giataId: this.selectedAccommodation.giataID,
                hotelName: this.selectedAccommodation.name,
                position: 0,
            });
        } else {
            const selected = this.selectedRooms.rooms.map((e) => e.roomOffers).flat();

            this.selectedAccommodation.rooms.some(
                (room: { roomOffers: { id: PropertyKey; personCount: number }[] }) => {
                    room.roomOffers.some((offer: { id: any; personCount: number }) => {
                        if (selected.find((e) => e.id === offer.id)) {
                            offer.personCount = 1;
                        }
                    });
                }
            );
            this.modalService.createDetailsPage(MobAccommodationRoomsComponent, {
                data: {
                    accommodation: this.selectedAccommodation,
                    selectedRooms: this.selectedRooms,
                    selectedCartItem: this.selectedCartItem,
                    ratings: this.selectedAccommodation.ratings,
                    isUserLogged: this.isUserLogged,
                    roomsLeftForSelection: this.roomsLeftForSelection,
                    numberOfNights: this.selectedAccommodation.numberOfNights,
                    id: this.selectedAccommodation.giataID,
                },
                title: 'HOTELS.CHOOSE_ROOMS',
            });
        }
    }

    public onChangeHotel() {
        this.changeHotel.emit();
    }

    public onModify() {
        this.changeRooms.emit({
            giataId: this.selectedAccommodation.giataID,
            hotelName: this.selectedAccommodation.name,
            position: 0,
        });
    }

    public onCancel(cancelAll = false, optionId = ''): void {
        const optionIds: string[] = [];
        if (cancelAll) {
            for (const option of this.selectedAccommodationOptions) {
                optionIds.push(option.itineraryId);
            }
        }

        if (optionId !== '') {
            optionIds.push(optionId);
        }

        if (optionIds.length > 0) {
            this.cancel.emit(optionIds);
        }
    }

    public getRoomButtons(option: ICartSelectedItemBookingOption): string[] {
        let roomButtons: string[] = [];
        if (CartUtils.isCancellableOption([option]) && this.paymentCaptureMode !== PaymentCaptureMode.NOT_DONE) {
            roomButtons = ['cancel'];
        } else {
            this.buttons = [];
        }

        if (CartUtils.isFailed(option.status)) {
            roomButtons = ['update', 'remove'];
        }

        return roomButtons;
    }

    public isAllOptionsBooked(): boolean {
        return !this.selectedAccommodationOptions.some((option) => option.status !== CartStatus.STATUS_CONFIRMED);
    }

    public openTermsModal(roomOption: RoomOption) {
        const data = {
            roomCancellation: roomOption.roomCancellation,
            hasPromotions: roomOption.hasPromotions,
            discounts: roomOption.discounts,
            remarks: roomOption.remarks,
            isPayOnArrival: roomOption.payAt === 'Pay on arrival',
            checkInDateTime: this.selectedAccommodation.checkIn,
        };

        this.dialogService.openMainDialog({
            component: AccommodationTermsPopupComponent,
            showCloseBtn: true,
            title: 'HOTELS.TERMS_CONDITIONS',
            data: { ...data },
        });
    }

    private formSelectedAccommodation() {
        this.selectedCartItem = this.accommodationCartData.items[0].selectedItem;
        this.selectedAccommodation = this.selectedCartItem.data;
        this.selectedRooms = AccommodationsUtilsService.getCartRooms(this.selectedCartItem);
        this.selectedAccommodationOptions = this.selectedCartItem.bookingOptions;

        this.invalidCartItem =
            this.selectedAccommodationOptions.length === 1 &&
            !this.selectedAccommodationOptions.some(
                (option: ICartSelectedItemBookingOption) =>
                    option.status < CartStatus.STATUS_REJECTED || option.status === CartStatus.STATUS_PRICE_CHANGED
            );
    }

    private initializeActionBarButtons() {
        this.buttons = TzSelectedService.initializeActionBarButtons(
            this.selectedCartItem,
            this.fromBookings,
            this.serviceList,
            this.paymentCaptureMode,
            this.detailPage,
            this.selectedAccommodationOptions.length > 1
        );

        if (!this.fromBookings && !this.detailPage) {
            if (!CartUtils.hasBooked(this.selectedCartItem.bookingOptions)) {
                this.buttons.push('changeHotel');

                if (CartUtils.hasUnavailable(this.selectedCartItem.bookingOptions)) {
                    this.buttons.push('update');
                } else {
                    if (this.selectedAccommodationOptions.length > 1) {
                        this.buttons = this.buttons.concat(['changeRooms']);
                    } else {
                        this.buttons.push('changeRoom');
                    }
                }
            }

            for (let i = 0; i < this.selectedCartItem.bookingOptions.length; i++) {
                if (CartUtils.isBooked(this.selectedCartItem.bookingOptions[i].status)) {
                    this.roomButtons[i] = [];
                } else {
                    if (CartUtils.isFailed(this.selectedCartItem.bookingOptions[i].status)) {
                        this.roomButtons[i] = ['update', 'remove'];
                    } else {
                        this.roomButtons[i] = ['remove'];
                    }

                    if (!this.serviceList && !CartUtils.isFailed(this.selectedCartItem.bookingOptions[i].status)) {
                        this.roomButtons[i].push('debug');
                    }
                }
            }
        }
    }
}
