import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { AccommodationListItem } from '@schemas/modules/accommodations/accommodation.model';
import { ICartItemBookingOptionSelectedRoom } from '@schemas/modules/cart/cart-item-booking-option-selected-room';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { AccommodationRecOffersLoggedIn } from '@schemas/modules/accommodations/accommodation-rec-offers-logged-in.model';
import { TzServiceTabs } from '../../../tabsUtils/tz-tabs-service';
import { AccommodationsService } from '@services/modules/accommodations/services/accommodations.service';

@Component({
    selector: 'tz-mob-accommodations-list-item',
    templateUrl: './mob-accommodations-list-item.component.html',
    styleUrls: ['./mob-accommodations-list-item.component.scss'],
})
export class MobAccommodationsListItemComponent implements OnChanges, OnInit {
    @Input() accommodation!: AccommodationListItem;
    @Input() summary = false;
    @Input() serviceList = false;
    @Input() cheapest!: AccommodationRecOffersLoggedIn;
    @Input() selectedBookingOptions: ICartSelectedItemBookingOption[] = [];
    @Input() itemPrice = 0;
    @Input() totalPersons = 0;
    @Input() oldPrice = 0;
    @Output() selectHotel: EventEmitter<void> = new EventEmitter<void>();

    public changedPriceDiff = '';
    public cartRoom!: ICartItemBookingOptionSelectedRoom;
    public isShowAddress = true;
    public isShowReview = true;

    constructor(private serviceTabs: TzServiceTabs, private accommodationsService: AccommodationsService) {}

    public ngOnInit(): void {
        this.initData();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (
            changes['selectedBookingOptions'] &&
            this.selectedBookingOptions.length === 1 &&
            this.selectedBookingOptions[0].selectedRoom
        ) {
            this.cartRoom = this.selectedBookingOptions[0].selectedRoom;
        }
    }

    public starsArray(i: number): number[] {
        return new Array(i);
    }

    public onSelectHotel(): void {
        this.selectHotel.emit();
    }

    private initData(): void {
        if (this.oldPrice > 0) {
            this.changedPriceDiff = this.serviceTabs.updatePriceChanging(this.oldPrice, this.itemPrice);
        }
    }
}
