import { Injectable } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { Subject } from 'rxjs';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { Room, RoomPrice } from '@schemas/modules/accommodations/room';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { AccommodationListMetadata } from '@schemas/modules/accommodations/accommodation-list-metadata';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { AccommodationsService } from '@services/modules/accommodations/services/accommodations.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalService } from '@services/modal.service';
import { UserSearchService } from '@services/searchEngine/services/user-search-service';
import { AccommodationTermsPopupComponent } from './accommodation/components/accommodation-terms-popup/accommodation-terms-popup.component';
import { ServiceId } from '@core/constants/service-id.const';
import { StateFrom } from '@core/constants/state-from.const';
import { MainDialogSize } from '@shared/schemas/dialog.model';
import { takeUntil } from 'rxjs/operators';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';

@Injectable({
    providedIn: 'root',
})
export class AccommodationDetailsUtilService {
    public readonly maxRoomQuantity = 4;
    public accommodation!: Accommodation;
    public bookingOptions: ICartSelectedItemBookingOption[] = [];
    public isUserLogged!: boolean;
    public fromTzMap = false;
    public isNextSummary = false;
    public cartRooms: any = [];
    public confirmableRoom: number | null = null;
    public roomsLeftForSelection = this.maxRoomQuantity;
    public fromHotelsMap = false;
    public hasBooked = false;
    public destroy$: Subject<boolean> = new Subject<boolean>();
    public roomsQuantity = 0;
    public totalPrice = 0;
    public selectedHotel!: ICartItem;
    public accommodationListMetadata?: AccommodationListMetadata;
    public selectRecommended = false;

    constructor(
        private accommodationsDataService: AccommodationsDataService,
        private accommodationsService: AccommodationsService,
        private cartRouterService: CartRoutingService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private modalService: ModalService,
        private userSearchService: UserSearchService
    ) {}

    public getRoomPrice(roomPrice: RoomPrice, servicePrices: RoomPrice[]): number {
        if (servicePrices?.find((item: RoomPrice) => item.id === roomPrice.id)) {
            servicePrices = servicePrices.filter((value: RoomPrice) => value.id !== roomPrice.id);
        } else {
            servicePrices.push(roomPrice);
        }

        return servicePrices.reduce((accumulator: number, value: RoomPrice) => accumulator + value.price, 0);
    }

    public openTermsModal(roomOption: RoomOption): void {
        const data = {
            roomCancellation: roomOption.roomCancellation,
            hasPromotions: roomOption.hasPromotions,
            discounts: roomOption.discounts,
            remarks: roomOption.remarks,
            isPayOnArrival: roomOption.payAt === 'Pay on arrival',
            checkInDateTime: this.accommodation.checkIn,
        };

        this.dialogService.openMainDialog({
            component: AccommodationTermsPopupComponent,
            showCloseBtn: true,
            title: 'HOTELS.TERMS_CONDITIONS',
            data: { ...data },
        });
    }

    public addAccommodationItemToCart(accommodation: Accommodation): void {
        if (accommodation.broker === 'Hotelbeds') {
            const rateComments: { comment: string; roomName: string }[] = [];
            accommodation.rooms.forEach((room: Room) => {
                if (room.roomOffers) {
                    room.roomOffers.forEach((offer: RoomOption) => {
                        if (offer.personCount > 0 && offer.rateComments !== '') {
                            const rateComment = {
                                roomName: `${room.displayName.toUpperCase()} ${
                                    offer.personCount > 1 ? 'x ' + offer.personCount : ''
                                } (${offer.meal.toUpperCase()})`,
                                comment: offer.rateComments,
                            };
                            let avail = false;
                            rateComments.forEach((rc) => {
                                if (rc.comment === rateComment.comment) {
                                    rc.roomName = rc.roomName + ', ' + rateComment.roomName;
                                    avail = true;
                                }
                            });

                            if (!avail) {
                                rateComments.push(rateComment);
                            }
                        }
                    });
                }
            });

            if (rateComments.length) {
                this.dialogService
                    .openConfirmationDialog(
                        {
                            confirmBtnTitle: this.translateService.instant('POPUPS.CONTINUE'),
                            cancelBtnTitle: this.translateService.instant('POPUPS.CANCEL'),
                            title: this.translateService.instant('POPUPS.RATE_COMMENTS.TITLE'),
                            multipleContent: [`<b>${rateComments[0].roomName}</b>`, rateComments[0].comment],
                            containerClass: 'tz-hb-rate-comment',
                        },
                        { mainDialogSize: MainDialogSize.LARGE, dialogPanelClasses: ['tz-rate-comment-dialog'] }
                    )
                    .afterClosed()
                    .pipe(takeUntil(this.destroy$))
                    .subscribe((dialogReturn: TzDialogReturn) => {
                        if (dialogReturn === CONFIRM) {
                            this.addItem(accommodation);
                        }
                    });
            }
        } else {
            this.addItem(accommodation);
        }
    }

    public checkRoomsSold(accommodation: Accommodation): void {
        if (!accommodation.noRoom || this.fromTzMap) {
            return;
        }

        if (this.fromHotelsMap) {
            this.dialogService
                .openConfirmationDialog({
                    title: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.TITLE'),
                    multipleContent: [this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.MESSAGE')],
                    confirmBtnTitle: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.CONTINUE'),
                    cancelBtnTitle: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.BACK'),
                    icon: { name: 'no-hotel-dialog', iconStyle: { 'width.px': 105.89, 'height.px': 96.4 } },
                })
                .afterClosed()
                .subscribe((confirm) => {
                    if (confirm) {
                        return;
                    }

                    this.onBackToResultsPage();
                    return;
                });
        } else {
            this.dialogService.openSnackBar({
                message: this.translateService.instant('NOTIFS.ACC_NOT_AVAILABLE'),
            });
        }
    }

    private onBackToResultsPage(backToMap?: boolean): void {
        if (!backToMap) {
            this.accommodationsService.setMapHotel(undefined);
        }

        this.cartRouterService.goToState(ServiceId.ACC, undefined, false, StateFrom.ACC_DETAILS);
    }

    private addItem(accommodation: Accommodation): void {
        this.accommodationsDataService.addAccommodationToCart(accommodation).subscribe(
            () => {
                this.accommodationsService.setMapHotel(undefined);
                this.userSearchService.searchCheckDep(ServiceId.ACC, this.isNextSummary, false, false, true);

                this.modalService.dismiss(true);
            },
            (error: any) => {
                console.error(error);
            }
        );
    }
}
