import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { Flight } from '@schemas/modules/flights/flight';

@Component({
    selector: 'tz-flight-list-item-description',
    templateUrl: './flight-list-item-description.component.html',
    styleUrls: ['./flight-list-item-description.component.scss'],
})
export class FlightListItemDescriptionComponent implements OnInit {
    @Input() flight!: Flight;

    @Output() openTerms: EventEmitter<void> = new EventEmitter<void>();
    @Output() openCheckinAllowanceInfo: EventEmitter<void> = new EventEmitter<void>();

    constructor() {}

    ngOnInit() {}

    onOpenTermsAndConditions($event: Event) {
        $event.stopPropagation();
        this.openTerms.emit();
    }

    onOpenCheckinAllowance($event: Event): void {
        $event.stopPropagation();
        this.openCheckinAllowanceInfo.emit();
    }
}
