import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { TrainClass } from '@schemas/modules/trains/train-class';
import { TRAIN } from '@schemas/modules/trains/train';
import { Route } from '@schemas/modules/trains/train-route';

@Component({
    selector: 'tz-mob-train-route-scheme',
    templateUrl: './mob-train-route-scheme.component.html',
    styleUrls: ['./mob-train-route-scheme.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobTrainRouteSchemeComponent implements OnInit {
    @Input() train!: TRAIN;

    public trainSummary!: TrainClass | TRAIN;
    public numberOfDays!: number;
    public routes: Route[] = [];

    public ngOnInit(): void {
        this.initData();
    }

    private initData(): void {
        this.numberOfDays = this.train.trainClasses[0]?.noOfDays;
        this.routes = this.train.trainClasses[0]?.route;
        this.setLayover(this.routes);
    }

    private setLayover(routes: Route[]): void {
        routes.forEach((route: Route) => {
            if (routes.length > 1 && !route.layoverIn) {
                route.layoverIn = routes[routes.indexOf(route) + 1].layoverIn;
                route.layoverDuration = routes[routes.indexOf(route) + 1].layoverDuration;
            }
        });
    }
}
