import { Component, Input, OnInit } from '@angular/core';
import { TrainClass } from '@schemas/modules/trains/train-class';
import { DialogService } from '@services/dialog.service';
import { TrainTermsComponent } from '../terms/terms.component';
import { MainDialogSize } from '@shared/schemas/dialog.model';

@Component({
    selector: 'tz-trains-class-details',
    templateUrl: './trains-class-details.component.html',
    styleUrls: ['./trains-class-details.component.scss'],
})
export class TrainsClassDetailsComponent implements OnInit {
    @Input() trainClass!: TrainClass;
    @Input() hideTitle = false;

    constructor(private dialogService: DialogService) {}
    ngOnInit(): void {}

    public openTrainTerms() {
        this.dialogService.openServiceDialog(TrainTermsComponent, {
            width: undefined,
            maxHeight: '90vh',
            panelClass: 'tz-terms-dialog-large',
            maxWidth: '940px',
            data: {
                operator: this.trainClass.operator,
                terms: this.trainClass.trainClass.ClassDescription.termsandConditionsList,
            },
        });
    }
}
