import { Component, Input, OnInit, Inject, ChangeDetectionStrategy, HostListener } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { Gallery } from '@schemas/modules/accommodations/accommodation-gallery.model';
import { KeyType } from '@core/constants/keyboard-key-types.const';
import { DialogService } from '@services/dialog.service';

const maxGellaryCount = 48;
const galleryImgRows = 12;

@Component({
    selector: 'tz-accommodation-gallery',
    templateUrl: './accommodation-gallery.component.html',
    styleUrls: ['./accommodation-gallery.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccommodationGalleryComponent implements OnInit {
    @Input() galleryData: {
        name: string;
        giataId: string;
        code: string;
        imageProvider: string;
        images: Gallery[];
        selectedIdx: number;
    };

    public currentImg!: Gallery;
    public images: Gallery[] = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<AccommodationGalleryComponent>,
        private dialogService: DialogService
    ) {
        this.galleryData = ToolsService.deepCopy(data);
    }

    get currentImgIdx(): number {
        return this.images.findIndex((img) => img.selected);
    }

    @HostListener('document:keyup', ['$event'])
    onKeyUp(event: KeyboardEvent) {
        switch (event.key) {
            case KeyType.LEFT_ARROW:
            case KeyType.RIGHT_ARROW:
                this.plusSlides(event.key === KeyType.LEFT_ARROW ? -1 : 1);
                break;
            case KeyType.UP_ARROW:
            case KeyType.DOWN_ARROW:
                this.arrowUpDownSlides(event.key === KeyType.UP_ARROW);
                break;
            case KeyType.ESCAPE:
                this.close();
                break;
        }
    }

    ngOnInit() {
        this.galleryData.images.sort((a, b) => a.id - b.id);

        if (this.galleryData.images.length > maxGellaryCount) {
            this.galleryData.images = this.galleryData.images.splice(0, maxGellaryCount);
        }

        this.setSelectedImg();
    }

    public selectImage(id: number): void {
        this.resetSelectedImage();
        this.sortImages();

        this.images.forEach((img, idx) => {
            img.selected = false;

            if (img.id === id) {
                this.setCurrentImage(idx);
            }
        });
    }

    public imgCallback(resp: any): void {
        if (resp.status === 200) {
            const img = this.galleryData.images[Number(resp.id)];
            this.images.push(img);
            this.sortImages();
            if (img.selected) {
                this.selectImage(img.id);
            }
        } else {
            if (resp.selected) {
                this.setCurrentImage(0);
            }
        }
    }

    public plusSlides(n: number): void {
        if (this.currentImgIdx === -1 || !this.images[this.currentImgIdx]) {
            return;
        }

        let selectedIdx = this.currentImgIdx;
        this.resetSelectedImage();
        selectedIdx += n;

        if (selectedIdx < 0) {
            selectedIdx = this.images.length - 1;
        }
        if (selectedIdx > this.images.length - 1) {
            selectedIdx = 0;
        }
        this.setCurrentImage(selectedIdx);
    }

    public reformImageUrl(url: string, size: string): string {
        if (!url || !size) {
            return url;
        }
        return url.replace('{size}', size);
    }

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }

    private sortImages(): void {
        this.images.sort((a, b) => a.id - b.id);
    }

    private resetSelectedImage(): void {
        this.currentImg = {} as Gallery;

        this.images.forEach((img) => {
            img.selected = false;
        });
    }

    private setCurrentImage(selectedIdx: number) {
        this.resetSelectedImage();
        if (this.images.length > 0 && this.images.length > selectedIdx) {
            this.images[selectedIdx].selected = true;
            this.currentImg = this.images[selectedIdx];
        }
    }

    /*dynamic loading of image may result in duplicate selection.
        so when user select image manually check whether any one of image is selected to
        ensure the default image is loaded before selection*/
    private setSelectedImg(): boolean {
        let isSelected = false;
        for (const img of this.galleryData.images) {
            img.selected = img.id === this.galleryData.selectedIdx;

            if (img.selected === true) {
                isSelected = true;
            }
        }

        if (!isSelected && this.galleryData.images.length > 0) {
            this.galleryData.images[0].selected = true;
            isSelected = true;
        }
        return isSelected;
    }

    private arrowUpDownSlides(moveUp: boolean) {
        const totalImages = this.images.length - 1;
        const selectedIdx = this.currentImgIdx;
        const moveToIdx = selectedIdx + (moveUp ? -galleryImgRows : galleryImgRows);
        if (moveToIdx < 0 || moveToIdx >= totalImages) {
            return;
        }

        this.setCurrentImage(moveToIdx);
    }
}
