import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AccommodationListItem } from '@schemas/modules/accommodations/accommodation.model';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { AccommodationRecOffersLoggedIn } from '@schemas/modules/accommodations/accommodation-rec-offers-logged-in.model';
import { BestValType } from '@schemas/modules/search/search';
import { ResultsTools } from '@services/modules/results/services/tools.shared.services';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-accommodation-list-item',
    templateUrl: './accommodation-list-item.component.html',
    styleUrls: ['./accommodation-list-item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccommodationListItemComponent implements OnInit {
    @Input() accommodation!: AccommodationListItem;
    @Input() cheapest!: AccommodationRecOffersLoggedIn;
    @Input() selectedBookingOptions: ICartSelectedItemBookingOption[] = [];
    @Input() accommodationType: 'main' | 'selected' | 'myTrip' = 'main';
    @Input() selectable = true;
    @Input() serviceList = false;
    @Input() oldPrice = 0;
    @Input() isSelected = false;
    @Input() itemPrice = 0;
    @Input() totalPersons = 0;
    @Output() selectHotel: EventEmitter<void> = new EventEmitter<void>();
    @Output() openAccommodationInMap: EventEmitter<AccommodationListItem> = new EventEmitter<AccommodationListItem>();
    public bestValType: BestValType | undefined;

    constructor() {}

    get isMainBlock(): boolean {
        return this.accommodationType === 'main';
    }

    ngOnInit(): void {
        if (this.accommodation && this.accommodation.bestValType) {
            this.bestValType = ResultsTools.getBestValType(this.accommodation.bestValType, ServiceId.ACC);
        }
    }

    public truncateWithEllipses(text: string) {
        return text.substr(0, 40) + (text.length > 40 ? '...' : '');
    }

    public onSelectHotel() {
        if (this.accommodationType !== 'myTrip') {
            this.selectHotel.emit();
        }
    }

    public openInMap(): void {
        this.openAccommodationInMap.emit(this.accommodation);
    }
}
