import { Injectable } from '@angular/core';
import { bestValTypeMapping, BestValType } from '@schemas/modules/search/search';
import { SearchUtilsService } from '@services/modules/search/utils/search.utils';
import { ServiceId } from '@core/constants/service-id.const';

// eslint-disable-next-line max-len
@Injectable({
    providedIn: 'root',
})
export class ResultsTools {
    public static getBestValType(bestValInfo: bestValTypeMapping, service: ServiceId): BestValType | undefined {
        if (!bestValInfo) {
            return undefined;
        }

        if (service !== ServiceId.INVALID) {
            let sort = SearchUtilsService.getSortingValue(service);
            if (!sort && (service === ServiceId.CAR_RENTAL || service === ServiceId.TRANSFER)) {
                sort = 'cheapest';
            }
            switch (sort) {
                case 'quickest':
                    if (bestValInfo[BestValType.QUICKEST]) {
                        return BestValType.QUICKEST;
                    }
                    break;
                case 'cheapest':
                    if (bestValInfo[BestValType.CHEAPEST]) {
                        return BestValType.CHEAPEST;
                    }
                    break;
                default:
                    return this.getDefaultBestValType(bestValInfo);
            }
        }

        return this.getDefaultBestValType(bestValInfo);
    }

    private static getDefaultBestValType(bestValInfo: bestValTypeMapping): BestValType | undefined {
        if (bestValInfo[BestValType.RECOMMENDED]) {
            return BestValType.RECOMMENDED;
        }

        if (bestValInfo[BestValType.CHEAPEST]) {
            return BestValType.CHEAPEST;
        }

        if (bestValInfo[BestValType.QUICKEST]) {
            return BestValType.QUICKEST;
        }

        return undefined;
    }
}
