import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CartStatus, PaymentCaptureMode } from '@core/constants/cart-status';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CombinationId } from '@schemas/modules/core/base-types';
import { TzSelectedService } from '../../../selectedUtils/tz-selected-service';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-transfers-cart-item',
    templateUrl: './transfer-cart-item.component.html',
    styleUrls: ['./transfer-cart-item.component.scss'],
})
export class TransferCartItemComponent implements OnInit {
    @Input() serviceList = false;
    @Input() combId!: CombinationId;
    @Input() transferCartItem!: ICartItem;
    @Input() fromBookings = false;
    @Input() paymentCaptureMode = PaymentCaptureMode.NORMAL_MODE;
    @Input() showActionBar!: boolean;
    @Input() showTitle!: boolean;
    @Input() totalPassengers = 0;
    @Input() isSummary = false;
    @Input() public selectedItemsQuantity = 0;
    @Input() public isFirst!: boolean;

    @Output() remove: EventEmitter<string> = new EventEmitter<string>();
    @Output() changeTransfer: EventEmitter<Transfer> = new EventEmitter<Transfer>();
    @Output() modify: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();
    @Output() selectedId: EventEmitter<string> = new EventEmitter<string>();
    public buttons: string[] = [];
    public optionId!: string;
    public invalidCartItem = false;

    constructor(public platformService: PlatformService) {}

    ngOnInit() {
        this.optionId = this.transferCartItem.selectedItem.bookingOptions[0].optionId;
        this.initializeActionBarButtons();

        this.invalidCartItem = !this.transferCartItem.selectedItem.bookingOptions.some(
            (option) =>
                !(option.status >= CartStatus.STATUS_REJECTED && option.status !== CartStatus.STATUS_PRICE_CHANGED)
        );
    }

    public onRemove(): void {
        this.remove.emit(this.optionId);
    }

    public onChangeTransfer(data: Transfer): void {
        this.changeTransfer.emit(data);
    }

    public onCancel(): void {
        this.cancel.emit();
    }

    public setSelectedId(itemId: string): void {
        this.selectedId.emit(itemId);
    }

    private initializeActionBarButtons() {
        this.buttons = TzSelectedService.initializeActionBarButtons(
            this.transferCartItem.selectedItem,
            this.fromBookings,
            this.serviceList,
            this.paymentCaptureMode,
            false
        );
        if (!this.fromBookings && !CartUtils.hasBookingStarted(this.transferCartItem.selectedItem.bookingOptions)) {
            this.buttons.push('changeTransfer');
        }
    }
}
