import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Transfer } from '@schemas/modules/transfers/transfer';
import { BestValType } from '@schemas/modules/search/search';
import { ResultsTools } from '@services/modules/results/services/tools.shared.services';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-transfer-list-item',
    templateUrl: './transfer-list-item.component.html',
    styleUrls: ['./transfer-list-item.component.scss'],
})
export class TransferListItemComponent implements OnInit {
    @Input() transfer: Transfer | null | undefined;
    @Input() selectable = true;
    @Input() showBestValLabel = false;
    @Input() cancelable = false;
    @Input() oldPrice = 0;
    @Input() totalPassengers = 0;
    @Output() selectedTransfer: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<void> = new EventEmitter<void>();

    public bestValType: BestValType | undefined;
    public opened = false;

    constructor() {}

    ngOnInit(): void {
        if (this.transfer && this.showBestValLabel && this.transfer.bestValType) {
            this.bestValType = ResultsTools.getBestValType(this.transfer.bestValType, ServiceId.TRANSFER);
        }
    }

    public selectTransfer(): void {
        this.selectedTransfer.emit();
    }

    public onCancel(): void {
        this.cancel.emit();
    }
}
