import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { PaymentCaptureMode } from '@core/constants/cart-status';
import { ICartSelectedItem } from '@schemas/modules/cart/cart-selected-item';

export class TzSelectedService {
    public static initializeActionBarButtons(
        cartItem: ICartSelectedItem,
        fromBookings: boolean,
        isServiceList: boolean,
        paymentCaptureMode: number,
        cancelOnly: boolean,
        hasMultipleItems?: boolean
    ): string[] {
        let buttons: string[] = [];

        if (fromBookings) {
            return TzSelectedService.fromBookingButtons(cartItem, paymentCaptureMode, cancelOnly, hasMultipleItems);
        }

        if (!hasMultipleItems && CartUtils.hasPriceChange(cartItem.bookingOptions)) {
            buttons = ['accept'];
        }
        if (hasMultipleItems) {
            if (CartUtils.hasBooked(cartItem.bookingOptions)) {
                if (
                    CartUtils.hasUnavailable(cartItem.bookingOptions) ||
                    CartUtils.hasNoBookingStarted(cartItem.bookingOptions)
                ) {
                    buttons = buttons.concat(['update']);
                }
                return buttons;
            } else if (CartUtils.hasBookingStarted(cartItem.bookingOptions)) {
                // return buttons.concat(['update', 'removeAll']);
                return buttons.concat(['update', 'remove']);
            }
        } else {
            if (CartUtils.hasBooked(cartItem.bookingOptions)) {
                return buttons;
            } else if (CartUtils.hasBookingStarted(cartItem.bookingOptions)) {
                return buttons.concat(['update', 'remove']);
            }
        }

        if (!isServiceList && !hasMultipleItems) {
            buttons = buttons.concat(['debug']);
        }

        /*if (hasMultipleItems) {
            buttons = buttons.concat(['removeAll']);
        } else {
            buttons = buttons.concat(['remove']);
        }*/
        buttons = buttons.concat(['remove']);

        return buttons;
    }

    private static fromBookingButtons(
        cartItem: ICartSelectedItem,
        paymentCaptureMode: number,
        cancelOnly: boolean, // when true only show cancel action when needed
        isCancelAll?: boolean
    ): string[] {
        let buttons: string[] = [];

        if (
            CartUtils.isCancellableOption(cartItem.bookingOptions) &&
            paymentCaptureMode !== PaymentCaptureMode.NOT_DONE
        ) {
            if (isCancelAll) {
                buttons = ['cancelAll'];
            } else {
                buttons = ['cancel'];
            }
        }
        if (!cancelOnly && CartUtils.hasUnavailable(cartItem.bookingOptions)) {
            buttons = ['update', 'remove'];
        }

        return buttons;
    }
}
