import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { TzServiceTabs } from '../../../../tabsUtils/tz-tabs-service';

@Component({
    selector: 'tz-mob-flight-list-item',
    templateUrl: './mob-flight-list-item.component.html',
    styleUrls: ['./mob-flight-list-item.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobFlightListItemComponent implements OnInit {
    @Input() isSelected = false;
    @Input() flight!: Flight;
    @Input() totalPassengers = 0;
    @Input() oldPrice = 0;
    @Input() packages: Flight[] | undefined = [];
    public selectedCartItem: ICartItem | undefined;
    public changedPriceDiff = '';

    constructor(private serviceTabs: TzServiceTabs) {}

    public ngOnInit(): void {
        this.initData();
    }

    private initData(): void {
        if (this.oldPrice > 0) {
            this.changedPriceDiff = this.serviceTabs.updatePriceChanging(this.oldPrice, this.flight.prices.listPrice);
        }
    }
}
