import { ChangeDetectionStrategy, Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';

@Component({
    selector: 'tz-mob-accommodation-details',
    templateUrl: './mob-accommodation-details.component.html',
    styleUrls: ['./mob-accommodation-details.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccommodationDetailsComponent implements OnInit {
    @Input() accommodation!: Accommodation;
    @Input() accommodationReviews!: AccommodationReviews;
    @Input() nearbyAttractions!: any;
    @Output() showRooms = new EventEmitter<void>();

    constructor() {}

    public ngOnInit(): void {
        this.nearbyAttractions = this.accommodation?.roomDetails?.nearByLocations;
    }

    public transformToMeters(distance: string): string {
        const distanceVal = +distance.split('k')[0];
        if (distanceVal < 1) {
            return distanceVal * 1000 + 'm';
        }
        return distance;
    }

    public formatDistance(d: number): string {
        return d < 0.1 ? Math.round(d * 1000) + 'm' : d.toFixed(1) + 'km';
    }
}
