import { Component, Inject, Input, ChangeDetectionStrategy } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ToolsService } from '@core/utils/tools-service';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { DialogService } from '@services/dialog.service';

@Component({
    selector: 'tz-car-rental-package-dialog',
    templateUrl: './car-rental-package-dialog.component.html',
    styleUrls: ['./car-rental-package-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CarRentalPackageDialogComponent {
    @Input() selectedPackage!: CarRentalPackage;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<CarRentalPackageDialogComponent>,
        private dialogService: DialogService
    ) {
        this.selectedPackage = ToolsService.deepCopy(data.selectedPackage);
    }

    get mandatory() {
        return this.packageInfo.mandatory;
    }

    get packageInfo() {
        return this.selectedPackage.packageInfo;
    }

    public close(): void {
        this.dialogService.closeDialog(this.mdDialogRef);
    }
}
