import { Component, Input, OnInit } from '@angular/core';

@Component({
    selector: 'tz-flight-train-skeleton-loader',
    templateUrl: './flight-train-skeleton-loader.component.html',
    styleUrls: ['./flight-train-skeleton-loader.component.scss'],
})
export class FlightTrainSkeletonLoaderComponent implements OnInit {
    @Input() twoWay = false;

    public transports!: number[];
    public transportItems!: number[];
    private transportsCount = 20;
    private transportItemsCount = 1;

    constructor() {}

    ngOnInit() {
        if (this.twoWay) {
            this.transportItemsCount = 2;
        }

        this.setTransports();
    }

    private setTransports(): void {
        this.transports = new Array(this.transportsCount);
        this.transportItems = new Array(this.transportItemsCount);
    }
}
