import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { Flight } from '@schemas/modules/flights/flight';

@Component({
    selector: 'tz-flights-pricing-plans',
    templateUrl: './flights-pricing-plans.component.html',
    styleUrls: ['./flights-pricing-plans.component.scss'],
})
export class FlightsPricingPlansComponent implements OnInit {
    @Input() package!: Flight;
    @Input() selected = false;
    @Input() luggageBlockCount!: number;
    @Output() selectPackage: EventEmitter<void> = new EventEmitter<void>();
    @Output() luggageBlockCountChange: EventEmitter<number> = new EventEmitter<number>();

    constructor() {}

    get luggageBlockMinHeight(): number {
        const count = this.luggageBlockCount;
        const luggageTextHeigh = 44;
        const luggageSpacing = 16;

        const toatlTextHeight = count * luggageTextHeigh;
        const totalMargin = count > 1 ? (count - 1) * luggageSpacing : 0;
        return toatlTextHeight + totalMargin;
    }

    ngOnInit(): void {
        // console.log('this.package');
        // console.log(this.package);
    }

    public onSelectClicked() {
        this.selectPackage.emit();
    }

    public onLuggageBlockCountChange(count: number): void {
        this.luggageBlockCountChange.emit(count);
    }
}
