import { Component, OnDestroy, OnInit } from '@angular/core';
import { Flight } from '@schemas/modules/flights/flight';
import { ModalService } from '@services/modal.service';
import { FlightsService } from '@services/modules/transport/flights/flights.service';
import { takeUntil } from 'rxjs/operators';
import { ICart } from '@schemas/modules/cart/cart';
import { Observable, Subject } from 'rxjs';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
@Component({
    selector: 'tz-mob-flight-details',
    templateUrl: './mob-flight-details.component.html',
    styleUrls: ['./mob-flight-details.component.scss'],
})
export class MobFlightDetailsComponent implements OnInit, OnDestroy {
    public flight!: Flight;
    public flightsPackages: Flight[] = [];
    public selectedFlight: Flight | undefined | null;
    public selectedFlightFromCart: IFlightsCartData | undefined | null;
    public totalPassengers = 0;
    public destroy$: Subject<boolean> = new Subject<boolean>();
    private data!: {
        flight: Flight;
        flightsPackages: Flight[];
        type: 'details' | 'action';
        totalPassengers: number;
    };

    constructor(private modalService: ModalService, private flightService: FlightsService) {}

    get isTypeAction(): boolean {
        return this.data.type !== 'details';
    }

    ngOnInit() {
        this.flight = this.data.flight;
        this.totalPassengers = this.data.totalPassengers;
        if (this.data.flightsPackages) {
            this.flightsPackages = this.data.flightsPackages;
        }
        this.selectedFlightFromCart = this.flightService.getSelectedFLightsFromCart();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onSelectPackage(flight: Flight): void {
        this.selectedFlight = flight;
    }

    public onSelectedFlightChange(selectedFlight: Flight | null) {
        if (!selectedFlight) {
            this.selectedFlight = null;
            return;
        }

        this.selectedFlight = selectedFlight;
    }

    public onConfirm(): void {
        const selectedFlight: Flight =
            this.data.flightsPackages && this.selectedFlight ? this.selectedFlight : this.flight;

        if (!this.selectedFlightFromCart?.isVirtual) {
            this.addItemToCart(selectedFlight)
                .pipe(takeUntil(this.destroy$))
                .subscribe(() => {
                    this.modalService.dismiss({ nextService: true });
                });
        } else {
            this.modalService.dismiss({ flight: selectedFlight });
        }
    }

    public addItemToCart(flight: Flight): Observable<ICart | undefined> {
        return this.flightService.addFlightToCart(flight);
    }

    public onOpenCheckinAllowance(flight: Flight): void {
        if (flight) {
            this.flightService.showFlightTrems(flight, true);
        }
    }
}
