import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Gallery } from '@schemas/modules/accommodations/accommodation-gallery.model';
import { ModalService } from '@services/modal.service';

@Component({
    selector: 'tz-mob-accommodation-gallery',
    templateUrl: './mob-accommodation-gallery.component.html',
    styleUrls: ['./mob-accommodation-gallery.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccommodationGalleryComponent implements OnInit {
    public data!: {
        name: string;
        imageProvider: string;
        images: Gallery[];
    };

    public images: Gallery[] = [];

    constructor(private modalService: ModalService) {}

    ngOnInit() {}

    public imgCallback(resp: any): void {
        if (resp.status === 200) {
            const img = this.data.images[Number(resp.id)];
            this.images.push(img);
            this.images.sort((a, b) => a.id - b.id);
        }
    }

    public reformImageUrl(url: string, size: string): string {
        if (!url || !size) {
            return url;
        }
        return url.replace('{size}', size);
    }

    public showRooms(): void {
        this.modalService.dismiss({ showRooms: true });
    }

    public showImgage(id: number): void {
        this.modalService.dismiss({ imageId: id });
    }
}
