import { Component, OnInit, Input, Output, EventEmitter, OnChanges, OnDestroy } from '@angular/core';

import { FilterItem, FilterOptionType } from '@schemas/modules/core/filters/service-filter';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-filter-sections',
    templateUrl: './filter-sections.component.html',
    styleUrls: ['./filter-sections.component.scss'],
})
export class FilterSectionsComponent implements OnInit {
    @Input() filterItem!: FilterItem;
    @Input() optionType!: FilterOptionType;
    @Output() filterItemChange: EventEmitter<FilterItem> = new EventEmitter<FilterItem>();

    public lastSliderIdx = -1;

    constructor(public platformService: PlatformService) {}

    ngOnInit() {
        this.setLastSliderIdx();
    }

    public changeSection(sectionName: string) {
        if (this.filterItem && Array.isArray(this.filterItem.sections)) {
            this.filterItem.sections.forEach((section) => {
                if (section.name === sectionName) {
                    section.active = true;
                    return;
                }
                section.active = false;
                this.setLastSliderIdx();
            });
        }
    }

    public onFilterItemChanged() {
        this.filterItemChange.emit(this.filterItem);
    }

    private setLastSliderIdx(): void {
        for (const section of this.filterItem.sections ?? []) {
            if (!section.active) {
                continue;
            }

            let slideIdx = -1;
            for (const [idx, option] of section.ogs.entries()) {
                if (option.slider) {
                    slideIdx = idx;
                }
            }

            this.lastSliderIdx = slideIdx;
        }
    }
}
