import { Injectable } from '@angular/core';
import { ResultService } from '@services/modules/core/dataservices/result.service';
import { ServiceId } from '@core/constants/service-id.const';
import { Observable, of } from 'rxjs';
import { ICart } from '@schemas/modules/cart/cart';
import { CarRental, RentalTerm } from '@schemas/modules/car-rentals/car-rental';
import { HttpRequestType } from '@core/constants/http-request-type';
import { CarRentalPackage } from '@schemas/modules/car-rentals/car-rental-package';
import { map, tap } from 'rxjs/operators';
import { CarRentalsResponse } from '@schemas/modules/car-rentals/car-rentals-response';

@Injectable({
    providedIn: 'root',
})
export class CarRentalDataService extends ResultService {
    public override readonly serviceId = ServiceId.CAR_RENTAL;

    private rentalTermsCache: {
        en: {
            [key: string]: RentalTerm[];
        };
        fr: {
            [key: string]: RentalTerm[];
        };
    } = {
        en: {},
        fr: {},
    };

    private static getRentalCarReq(carRentals: CarRental[]) {
        const carRentalRequest = carRentals.map((carRental: CarRental) => ({
            id: carRental.id,
            bookingOptions: [
                {
                    rentalPackageInfo: {
                        name: carRental.currentPackage.name,
                        packageKey: carRental.currentPackage.packageKey,
                        extrasSelected: {},
                        //extrasText: $scope.selectedRental.packageInfo.extrasText,
                    },
                },
            ],
        }));

        return carRentalRequest;
    }

    public addCarRentalToCart(selectedCarRentals: CarRental[] = []): Observable<ICart> {
        const carRentalCart = CarRentalDataService.getRentalCarReq(selectedCarRentals);
        return this.addToCart(carRentalCart);
    }

    public getCarRentalTerms(carRental: CarRental, selectedPackage: CarRentalPackage): Observable<RentalTerm[]> {
        const key = selectedPackage.rateQualifier + selectedPackage.productID;
        const lang = this.settingsService.langCode;
        if ((lang === 'en' || lang === 'fr') && this.rentalTermsCache[lang] && this.rentalTermsCache[lang][key]) {
            return of(this.rentalTermsCache[lang][key]);
        }

        const params = {
            entity: 'getRentalTerms',
            lang,
            rateQualifier: selectedPackage.rateQualifier,
            productID: selectedPackage.productID,
            fromLocationId: carRental.pickupLocation.locationId,
            toLocationId: carRental.dropOffLocation.locationId,
            closeLoader: true,
        };
        this.showLoader(true, 'LOADER_MESSAGES.TERMS_RENTAL');
        return this.apiCall(HttpRequestType.POST, params, params).pipe(
            tap((terms) => {
                if (lang === 'en' || lang === 'fr') {
                    this.rentalTermsCache[lang][key] = terms;
                }
            })
        );
    }

    public getCarRentalPackages(carRental: CarRental): Observable<CarRentalPackage[]> {
        const params = {
            packageName: carRental.currentPackage.packageKey,
            selected_id: carRental.id,
        };
        return this.searchServiceData(this.serviceId, params, false).pipe(
            map((response: CarRentalsResponse) => response.rentals.data[0].packagesAvailable)
        );
    }
}
