import { Component, OnDestroy, OnInit } from '@angular/core';
import { TRAIN } from '@schemas/modules/trains/train';
import { SupplierClass, TrainClass } from '@schemas/modules/trains/train-class';
import {
    SelectedOption,
    SupplierClassMinPrice,
    TrainSelection,
} from '@schemas/modules/trains/train-search-query-params';
import { ModalService } from '@services/modal.service';
import { TrainService } from '@services/modules/transport/trains/trains.service';
import { ToolsService } from '@core/utils/tools-service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
    selector: 'tz-mob-train-details',
    templateUrl: './mob-train-details.component.html',
    styleUrls: ['./mob-train-details.component.scss'],
})
export class MobTrainDetailsComponent implements OnInit, OnDestroy {
    public data!: {
        train: TRAIN;
        selectedOption: SelectedOption;
        onwardTrain: TRAIN;
        isCartItems: boolean;
        returnTrainChange$: Subject<TRAIN> | undefined;
        passengersNumber: number | undefined;
    };
    public train!: TRAIN;
    public availableTrainClasses!: SupplierClass[];
    public supplierClassMinPrice: SupplierClassMinPrice = {};
    public selectedTrain!: TrainSelection;
    public selectedOption: SelectedOption = {};
    public isOnwardSelected = false;
    public isCartOnwardSelected = false;
    public isCartReturnSelected = false;
    public totalPrice: number | undefined;
    public totalPriceCur: string | undefined;
    public isSupplierClassSelected = false;
    public selectedClassId: string | undefined;
    public selectedTrainSubClass!: number;
    public trainHasSubClass = false;
    public accordionTrainValues: string[] = [];
    public arrivalWaySeparatorInfo!: string;
    public departureWaySeparatorInfo!: string;
    private confirm = false;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private modalService: ModalService, private trainService: TrainService) {}

    ngOnInit() {
        this.train = this.data.train;

        if (this.data.selectedOption) {
            this.selectedOption = this.data.selectedOption;
        }

        this.fetchServicePrices();

        if (this.data.returnTrainChange$) {
            this.data.returnTrainChange$.pipe(takeUntil(this.destroy$)).subscribe((data: any) => {
                this.train = data;
                this.isSupplierClassSelected = false;
            });
        }

        this.checkSubClasses();
        this.setAccordionTrainValues();
        this.trainClassesConfig(this.train);

        this.arrivalWaySeparatorInfo = this.formatWaysSeparatorInfoString(this.data.train);
        this.departureWaySeparatorInfo = this.formatWaysSeparatorInfoString(this.data.onwardTrain);
    }

    formatWaysSeparatorInfoString(train: TRAIN): string {
        return train.arrival.location.city + ' - ' + train.departure.location.city;
    }

    ngOnDestroy() {
        this.destroy$.next(false);
        this.destroy$.complete();
    }

    public selectTicket(supplierClass?: TrainClass): void {
        if (supplierClass) {
            this.isSupplierClassSelected = true;

            this.selectedTrain = this.trainService.formTrainSelectionInfo(this.train, supplierClass, true, false);

            if (this.selectedOption.onward?.id && this.data.onwardTrain) {
                this.selectedOption = {
                    ...this.selectedOption,
                    return: this.selectedTrain,
                };
            }

            this.updateTotalPrice();
            this.selectedTrainSubClass = supplierClass.trainClass.ordinal;
        }
    }

    public selectedClass(id: string): void {
        if (!id) {
            this.isSupplierClassSelected = false;
        }

        this.selectedClassId = id;
    }

    public onToggleAccordion($event: Event) {
        if (!this.trainHasSubClass) {
            $event.stopPropagation();
        }
    }

    public onConfirm(): void {
        this.confirm = true;
        this.updateTotalPrice();
        if (!this.isSupplierClassSelected) {
            return;
        }

        if (this.data.isCartItems && !this.isCartOnwardSelected) {
            if (!this.data.onwardTrain) {
                this.modalService.dismiss({ selectedTrain: this.selectedTrain, isConfirmable: true });
                return;
            }

            const train = ToolsService.deepCopy(this.train);
            this.train = ToolsService.deepCopy(this.data.onwardTrain);
            this.data.onwardTrain = ToolsService.deepCopy(train);
            this.supplierClassMinPrice = {};
            this.fetchServicePrices(true);
            this.selectedOption = {
                onward: ToolsService.deepCopy(this.selectedTrain),
            };
            if (this.selectedOption.onward) {
                this.confirm = false;
                this.trainService.updateTrainSelection.next(this.selectedOption.onward);
            }
            this.isCartOnwardSelected = true;
            return;
        }

        if (this.isOnwardSelected && !this.data.isCartItems) {
            this.modalService.dismiss({ selectedTrain: this.selectedTrain, isConfirmable: true });
            return;
        }

        if (this.isCartOnwardSelected && this.data.isCartItems && this.isOnwardSelected) {
            this.modalService.dismiss({ selectedOption: this.selectedOption, isConfirmable: true });
            return;
        }

        if ((this.selectedOption.return && this.data.onwardTrain) || this.data.isCartItems) {
            this.isOnwardSelected = true;
            return;
        }

        this.modalService.dismiss({ selectedTrain: this.selectedTrain, isConfirmable: false });
    }

    private fetchServicePrices(selectedTrain = false): void {
        this.supplierClassMinPrice = this.trainService.getServicePrices(
            this.train,
            selectedTrain ? null : this.selectedTrain
        );
    }

    private updateTotalPrice(): void {
        if (this.data.onwardTrain && this.selectedOption.return && this.selectedOption.onward) {
            const onwardTrainPrices = this.trainService.getServicePrices(
                this.data.onwardTrain,
                this.selectedOption.onward
            );
            const returnTrainPrices = this.trainService.getServicePrices(this.train, this.selectedOption.return);

            const returnPrice = returnTrainPrices[this.selectedOption.return?.tzClass].listPrice;
            const onwardPrice = onwardTrainPrices[this.selectedOption.onward?.tzClass].listPrice;
            this.totalPrice = this.confirm ? onwardPrice + returnPrice : returnPrice;
            this.totalPriceCur = onwardTrainPrices[this.selectedOption.onward?.tzClass].listPriceCur;
        } else if (this.data.isCartItems) {
            const onwardTrainPrices = this.trainService.getServicePrices(this.train, this.selectedTrain);
            this.totalPrice = onwardTrainPrices[this.selectedTrain.tzClass].listPrice;
            this.totalPriceCur = onwardTrainPrices[this.selectedTrain.tzClass].listPriceCur;
        }

        if (!this.data.onwardTrain && this.selectedTrain) {
            const onwardTrainPrices = this.trainService.getServicePrices(this.train, this.selectedTrain);
            this.totalPrice = onwardTrainPrices[this.selectedTrain.tzClass].listPrice;
            this.totalPriceCur = onwardTrainPrices[this.selectedTrain.tzClass].listPriceCur;
        }
    }

    private checkSubClasses(): void {
        this.trainHasSubClass = false;

        const trainSubClassesObj: { [key: string]: TrainClass[] } = {};

        this.train.supplierClasses.map((supplierClasses) => {
            const key = supplierClasses.class;
            trainSubClassesObj[key] = this.train.trainClasses.filter(
                (tc) => tc.trainClass.tzClass === supplierClasses.class
            );
        });

        for (const [key, value] of Object.entries(trainSubClassesObj)) {
            if (value.length > 1) {
                this.trainHasSubClass = true;
            }
        }
    }

    private setAccordionTrainValues(): void {
        this.accordionTrainValues = [];

        if (!this.trainHasSubClass) {
            this.train.supplierClasses.map((element, index) => {
                this.accordionTrainValues.push(index.toString());
            });
        } else {
            this.accordionTrainValues.push('0');
        }
    }

    private trainClassesConfig(train: TRAIN): void {
        this.availableTrainClasses = train.supplierClasses.map((item: SupplierClass, index: number) => ({
            ...item,
            ...{ disabled: train.trainClasses[index].disabled },
        }));
    }
}
