import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-mob-accommodation-nearby',
    templateUrl: './mob-accommodation-nearby.component.html',
    styleUrls: ['./mob-accommodation-nearby.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobAccommodationNearbyComponent {
    @Input() accommodationReviews!: AccommodationReviews;

    constructor() {}

    public formatDistance(d: number): string {
        return d < 0.1 ? Math.round(d * 1000) + 'm' : d.toFixed(1) + 'km';
    }
}
