import { Component, Input } from '@angular/core';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { MobAccommodationReviewsComponent } from '../mob-accommodation-reviews/mob-accommodation-reviews.component';
import { ModalService } from '@services/modal.service';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';

@Component({
    selector: 'tz-mob-modal-page-link-block',
    templateUrl: './mob-modal-page-link-block.component.html',
    styleUrls: ['./mob-modal-page-link-block.component.scss'],
})
export class MobModalPageLinkBlockComponent {
    @Input() accommodation!: Accommodation;
    @Input() accommodationReviews: AccommodationReviews | null | undefined;

    constructor(private modalService: ModalService) {}

    public showReviewsMob(): void {
        this.modalService.createDetailsPage(MobAccommodationReviewsComponent, {
            data: { accommodationReviews: this.accommodationReviews },
            title: 'HOTELS.REVIEWS.RECENT',
        });
    }
}
